import { ADD_CARD, HIDE_LANE, MOVE_LANE, DELETE_LANE, ARCHIVE_ALL_CARDS } from '../../../constants/translated';
import { GUIDED, FLEXIBLE, FREEFORM } from '../../../actions/constants';

const hasDeleteOption = function (lane, options) {
	return !(
		(options.board_type === GUIDED && lane.value === '') ||
		(options.board_type === GUIDED && lane.cardsCount > 0) ||
		(options.board_type === GUIDED && lane.is_choice_lane) ||
		(options.board_type === FLEXIBLE && lane.cardsCount > 0) ||
		(lane.is_reference_lane && options.lane_filter)
	);
};

export function getLaneActionItems(lane) {
	const { id: laneId, options, cardsCount } = lane;
	let dropdownItems = [];
	if (!options.is2DBoard && options.can_add_task) {
		dropdownItems.push({ id: 'addCard', label: ADD_CARD, uuid: laneId });
	}
	dropdownItems = [
		...dropdownItems,
		{ id: 'hideLane', label: HIDE_LANE, uuid: laneId },
		{ id: 'moveLane', label: MOVE_LANE, uuid: laneId }
	];
	if (hasDeleteOption(lane, options)) {
		dropdownItems.push({ id: 'deleteLane', label: DELETE_LANE, uuid: laneId });
	}

	if (options.board_type === FREEFORM && cardsCount > 0) {
		dropdownItems.push({ id: 'archiveAllCards', label: ARCHIVE_ALL_CARDS, uuid: laneId });
	}
	return dropdownItems;
}
