import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-card';
import styles from './sn-vtb-freeform-card-editable.scss';
import {
	FREEFORM_CARD_ADDED,
	PLACEHOLDER_CARD_VALUE_UPDATE_CANCELLED,
	PLACEHOLDER_CARD_VALUE_UPDATED,
	REMOVE_FREEFORM_PLACEHOLDER_CARD
} from '../../../../actions/constants';
import { NEW_TASK_CREATED } from '../../../../constants/translated';

const view = () => {
	const placeholder = `${NEW_TASK_CREATED} ${new Date().toLocaleString('en-US', {
		hour: 'numeric',
		minute: 'numeric',
		hour12: true
	})}`;
	return (
		<now-card size="sm">
			<sn-inline-edit name="PLACEHOLDER_CARD" placeholder={placeholder} save-on-blur={false} editing />
		</now-card>
	);
};

/**
 * @seismicElement sn-vtb-freeform-card-editable
 * @summary Component to be rendered as a placeholder while adding card on freeform board.
 */
createCustomElement('sn-vtb-freeform-card-editable', {
	view,
	properties: {
		/**
		 * Card data object
		 * @type {Object}
		 */
		data: {
			default: {}
		}
	},
	actionHandlers: {
		[PLACEHOLDER_CARD_VALUE_UPDATE_CANCELLED]: ({ action, dispatch }) => {
			dispatch(REMOVE_FREEFORM_PLACEHOLDER_CARD);
		},
		[PLACEHOLDER_CARD_VALUE_UPDATED]: ({ action, dispatch, properties }) => {
			const { data } = properties;
			dispatch(REMOVE_FREEFORM_PLACEHOLDER_CARD);
			const order = data.order === -1 ? 0 : data.order;
			if (action.payload.newValue !== '')
				dispatch(FREEFORM_CARD_ADDED, {
					createCardInput: {
						boardId: data.board_id,
						shortDesc: action.payload.newValue,
						laneId: data.lane_id,
						swimlaneId: data.swim_lane_id,
						order: order
					}
				});
		}
	},
	styles
});
