import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { closestDeep, querySelectorDeep } from '@devsnc/library-vtb-commons';
import styles from './sn-vtb-lane-dest-placeholder.scss';

/**
 * @seismicElement sn-vtb-lane-dest-placeholder
 * @summary Component to be shown as a placeholder for lane at the drop zone during the drag event.
 */
createCustomElement('sn-vtb-lane-dest-placeholder', {
	view: ({ properties }) => {
		const { width } = properties;
		return (
			<div
				style={{
					width: `${properties.width}px`
				}}
			>
				<div
					className="sn-vtb-lane-dest-placeholder-body"
					style={{
						width: `${width}px`
					}}
				/>
			</div>
		);
	},
	properties: {
		width: { default: 0 },
		height: { default: 0 }
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ host }) => {
			const snBoardEl = closestDeep(host, '.sn-board');
			const destPlaceholder = querySelectorDeep('.sn-vtb-lane-dest-placeholder-body', host);
			destPlaceholder.style.height = snBoardEl.getBoundingClientRect().height + 'px';
		}
	},
	styles
});
