import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import styles from './sn-vtb-swimlane-header.scss';
import '../shared/sn-inline-edit/sn-inline-edit';
import '@servicenow/now-icon';
import '@servicenow/now-dropdown';
import {
	BUTTON_ICONIC_CLICKED,
	DROPDOWN_ITEM_CLICKED,
	COLLAPSE_ALL_SWIMLANES,
	EXPAND_ALL_SWIMLANES,
	TOGGLE_SWIMLANE_BODY,
	SWIMLANE_HEADER_UPDATED,
	OPEN_SWIMLANE_MOVE_MODAL,
	GUIDED,
	FREEFORM,
	OPEN_CONFIRM_DELETE_LANE_MODAL,
	LANE_HIDE_SELECTED,
	OPEN_ARCHIVED_ALL_CARDS_BY_LANE_MODAL
} from '../../../actions/constants';
import {
	CANCEL,
	COLLAPSE_SWIMLANE,
	DELETE,
	EXPAND_SWIMLANE,
	SWIMLANE_ACTIONS,
	ARCHIVE
} from '../../../constants/translated';
import { t } from 'sn-translate';
import If from '../../shared/if';
import { getSwimlaneActionItems } from './swimlane-actions-config';
import { querySelectorDeep } from '@devsnc/library-vtb-commons';

const { COMPONENT_PROPERTY_CHANGED } = actionTypes;
const SL_PREFIX = 'VTB#SWIMLANE_HEADER';
const SL_INLINE_EDIT_UPDATED = SL_PREFIX + '#VALUE_UPDATED';
import '@servicenow/now-tooltip';

const getHeaderClassNames = (collapsed, clone) => {
	return 'sn-vtb-swimlane-header ' + (collapsed ? 'collapsed ' : '') + (clone ? 'dnd-clone ' : '');
};

const view = (state, { updateState }) => {
	const setTargetRef = (ref, elm) => {
		updateState({
			path: 'targetRef.' + ref,
			value: elm,
			operation: 'set'
		});
	};
	const { collapseSLane: collapseSLaneRef, sLaneActions: actionsRef, name: nameRef } = state.targetRef;
	const { data: swimlane, collapsed, clone } = state.properties;
	const { name, options, addLaneButton } = swimlane;
	const boardType = options.board_type;
	const btnAriaLabel = collapsed ? EXPAND_SWIMLANE : COLLAPSE_SWIMLANE;
	return (
		<div className={getHeaderClassNames(collapsed, clone)} aria-label={name}>
			<If test={!clone}>
				<hr className={'separator ' + (addLaneButton ? 'till-add-lane' : '')} />
			</If>
			{/*Place tooltips on top of sticky position to avoid limiting z-index of tooltip*/}
			<now-tooltip target-ref={collapseSLaneRef} content={btnAriaLabel} />
			<now-tooltip target-ref={nameRef} content={name} />
			<now-tooltip target-ref={actionsRef} content={SWIMLANE_ACTIONS} />
			<div className="container">
				<div className="header-name">
					<p className="title">
						<sn-inline-edit
							hook-insert={({ elm }) => setTargetRef('name', elm)}
							hook-destroy={() => setTargetRef(null)}
							name={SL_PREFIX}
							value={swimlane.name}
							can-edit={boardType !== GUIDED}
							edit-on-focus={false}
							append-to-payload={{ id: swimlane.id }}
							config-aria={{
								label: {
									ariaLabel:
										t('Swimlane: {0}', swimlane.name) +
										t(boardType !== GUIDED ? ', press Enter to modify' : '')
								}
							}}
						/>
						<span className="sl-card-count">{swimlane.cardsCount}</span>
					</p>
				</div>

				<div className="header-controls">
					<now-button-iconic
						hook-insert={({ elm }) => setTargetRef('collapseSLane', elm)}
						hook-destroy={() => setTargetRef(null)}
						bare
						size="md"
						variant="tertiary"
						icon={collapsed ? 'chevron-down-fill' : 'chevron-up-fill'}
						configAria={{ 'aria-label': btnAriaLabel }}
						append-to-payload={{ id: swimlane.id, collapsed: !collapsed }}
					/>
					<now-dropdown
						className="swimlane-actions"
						hook-insert={({ elm }) => setTargetRef('sLaneActions', elm)}
						hook-destroy={() => setTargetRef(null)}
						icon="ellipsis-v-outline"
						size="md"
						variant="secondary"
						select="none"
						bare={true}
						hideCaret={true}
						config-aria={{ 'aria-label': SWIMLANE_ACTIONS }}
						items={getSwimlaneActionItems(swimlane, collapsed)}
						append-to-payload={{ id: swimlane.id }}
						hideLabel={true}
					/>
				</div>
			</div>
		</div>
	);
};

/**
 * @seismicElement sn-vtb-swimlane-header
 * @summary Component to be rendered in the swimlane header slot.
 */
export default createCustomElement('sn-vtb-swimlane-header', {
	view,
	properties: {
		/**
		 * Swimlane data object.
		 * @type {Object}
		 */
		data: { default: {}, reflect: true },
		/**
		 * Set this flag to false if you don't want swimlane to collapse.
		 * @type {boolean}
		 * @default {false}
		 */
		collapsed: { default: false, reflect: true },
		/**
		 * Set this flag to false if you don't want clone of swimlane header.
		 * @type {boolean}
		 * @default {false}
		 */
		clone: { default: false }
	},
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: ({ properties, host }) => {
			const { data: lane } = properties;
			const { isFocus } = lane;

			if (isFocus) {
				const focusableElem = querySelectorDeep('.sn-vtb-swimlane-header .container', host);
				focusableElem.focus();
			}
		},
		[SL_INLINE_EDIT_UPDATED]: ({ action, dispatch }) => {
			const {
				payload: { id, newValue, previousValue }
			} = action;
			dispatch(SWIMLANE_HEADER_UPDATED, { id, value: newValue });
		},
		[BUTTON_ICONIC_CLICKED]: ({ action, dispatch }) => {
			const { id, collapsed } = action.payload;
			dispatch(TOGGLE_SWIMLANE_BODY, {
				id,
				collapsed
			});
		},
		[DROPDOWN_ITEM_CLICKED]: ({ action, dispatch, properties }) => {
			const {
				id,
				item: { id: actionType }
			} = action.payload;
			const {
				data: { sys_id, board_id, name, options }
			} = properties;

			switch (actionType) {
				case 'collapseAllSl':
					dispatch(COLLAPSE_ALL_SWIMLANES);
					break;
				case 'expandAllSl':
					dispatch(EXPAND_ALL_SWIMLANES);
					break;
				case 'hideSwimlane':
					dispatch(LANE_HIDE_SELECTED, { laneId: sys_id });
					break;
				case 'deleteSwimlane':
					const boardType = options.board_type;
					let deleteModalContent = t('Are you sure you want to delete {0}?', name);
					if (boardType === FREEFORM) deleteModalContent += t(' Any cards in the swimlane will be archived.');
					const modalData = {
						headerLabel: t('Delete Swimlane'),
						content: deleteModalContent,
						footerActions: [
							{ label: DELETE, variant: 'primary-negative', id: 'deleteSwimlane' },
							{ label: CANCEL, variant: 'secondary', id: 'cancel' }
						]
					};
					dispatch(OPEN_CONFIRM_DELETE_LANE_MODAL, {
						laneId: sys_id,
						boardId: board_id,
						modalData
					});
					break;
				case 'archiveAllCards':
					let archiveAllCardsModalContent = t(
						'Are you sure you want to archive all cards in lane {0}?',
						name
					);

					const archiveAllCardsModalData = {
						headerLabel: t('Confirm Archive'),
						content: archiveAllCardsModalContent,
						footerActions: [
							{ label: ARCHIVE, variant: 'primary-negative', id: 'archiveAllCards' },
							{ label: CANCEL, variant: 'secondary', id: 'cancel' }
						]
					};
					dispatch(OPEN_ARCHIVED_ALL_CARDS_BY_LANE_MODAL, {
						laneId: sys_id,
						modalData: archiveAllCardsModalData
					});
					break;
				case 'moveSwimlane':
					dispatch(OPEN_SWIMLANE_MOVE_MODAL, { id });
					break;
			}
		},
		[actionTypes.COMPONENT_DOM_READY]: ({ host, dispatch }) => {
			const headerEl = querySelectorDeep('.sn-vtb-swimlane-header .container', host);
			if (headerEl) dispatch('REGISTER_ARROW_NAVIGABLE_ELEMENT', headerEl);
		}
	},
	styles,
	initialState: {
		targetRef: { default: {} }
	}
});
