import { CONFIRM_ARCHIVE_CARD, VTB_CARD_ARCHIVED } from '../../constants';
export default {
	[CONFIRM_ARCHIVE_CARD]: ({ dispatch, action }) => {
		const { payload } = action;
		const { cardId } = payload;
		const archiveCardInput = {
			cardId
		};
		dispatch(VTB_CARD_ARCHIVED, archiveCardInput);
		action.stopPropagation();
	}
};
