import { createAmbSubscriptionEffect } from '@servicenow/ui-effect-amb';
import {
	AMB_SUBSCRIPTION_INIT,
	AMB_SUBSCRIPTION_STARTED,
	AMB_SUBSCRIBED,
	AMB_SUBSCRIPTION_FAILED,
	AMB_UNSUBSCRIBED,
	AMB_MESSAGE_RECEIVED
} from './amb-constants';

let boardUpdateInProgress = false;
const messageQueue = [];
const logAction = ({ action }) => console.log('AMB Action Log', action.type, action.payload);

export const subscriptionActionHandlers = {
	[AMB_SUBSCRIPTION_INIT]: createAmbSubscriptionEffect('/vtb/:boardId', {
		subscribeStartedActionType: AMB_SUBSCRIPTION_STARTED,
		subscribeSucceededActionType: AMB_SUBSCRIBED,
		subscribeFailedActionType: AMB_SUBSCRIPTION_FAILED,
		unsubscribeSucceededActionType: AMB_UNSUBSCRIBED,
		messageReceivedActionType: AMB_MESSAGE_RECEIVED
	}),
	[AMB_SUBSCRIPTION_STARTED]: logAction,
	[AMB_SUBSCRIBED]: logAction,
	[AMB_SUBSCRIPTION_FAILED]: logAction,
	[AMB_UNSUBSCRIBED]: logAction,
	[AMB_MESSAGE_RECEIVED]: ({ action, dispatch }) => {
		const message = action.payload.data;
		if (message) {
			if (message.action === 'board.update.start') {
				boardUpdateInProgress = true;
			} else if (message.action === 'board.update.end') {
				boardUpdateInProgress = false;
			} else messageQueue.push(message);

			if (!boardUpdateInProgress) {
				while (messageQueue.length) {
					let m = messageQueue.shift();
					dispatch(m.responder_name, {
						message: m
					});
				}
			}
		}
	}
};
