import interact from 'interactjs';
import { actionTypes } from '@servicenow/ui-core';
import { closestDeep, querySelectorDeep } from '@devsnc/library-vtb-commons';
import '../components/board/shared/label-color-icon/sn-vtb-label-color-icon';

interact.dynamicDrop(true);

const draggable = (el, options) => {
	let elemToMove = null;
	let escapeListener;
	let mouseupListener;
	let currentScrollParent = null;
	let xScrollParent = null;
	let yScrollParent = null;

	const defaultConfig = {
		xScrollParent: 'body',
		yScrollParent: 'body'
	};

	const config = {
		...defaultConfig,
		...options
	};

	const listeners = {
		start: (event) => {
			const { left, top, width, height } = el.getBoundingClientRect();
			elemToMove = el.cloneNode(true);
			elemToMove.classList.add('ijs-active-dragging-item');

			let cloneStyle = `
				position: absolute;
				top: ${top}px;
				left: ${left}px;
				width: ${width}px;
				height: ${height}px;
				cursor: auto;
				transform-origin: 50% 50%;
				z-index :2;
			`;

			cssFromStr(elemToMove, cloneStyle);

			document.body.appendChild(elemToMove);
			document.body.addEventListener('keydown', (escapeListener = (e) => handleEscape(e, event)));
			// Initialize autoscroll container to horizontal scroll
			updateScrollParent(querySelectorDeep('.lanes-container'));
		},
		move: (event) => {
			const { dx, dy } = event;
			const dataX = elemToMove.getAttribute('data-x');
			const dataY = elemToMove.getAttribute('data-y');
			const initialX = parseFloat(dataX) || 0;
			const initialY = parseFloat(dataY) || 0;
			const newX = initialX + dx;
			const newY = initialY + dy;

			elemToMove.style.transform = `translate(${newX}px, ${newY}px)`;
			elemToMove.setAttribute('data-x', newX);
			elemToMove.setAttribute('data-y', newY);

			//Update the autoscroll container depending on the move direction
			const currentDropzoneElem = event.dropzone ? event.dropzone.target : null;
			// Is horizontal drag
			if (Math.abs(dx) !== 0 && dy === 0) {
				xScrollParent = closestDeep(currentDropzoneElem, config.xScrollParent);
				if (xScrollParent && currentScrollParent !== xScrollParent) updateScrollParent(xScrollParent);
			}
			// Is vertical drag
			else if (Math.abs(dy) !== 0 && dx === 0) {
				yScrollParent = closestDeep(currentDropzoneElem, config.yScrollParent);
				if (yScrollParent && currentScrollParent !== yScrollParent) updateScrollParent(yScrollParent);
			}
		},
		end: (event) => {
			document.body.removeChild(elemToMove);
			document.body.removeEventListener('keydown', escapeListener);
		}
	};

	function cssFromStr(el, cssStr) {
		const styles = cssStr.split(';');
		styles.forEach((style) => {
			const [key, value] = style.split(':').map((s) => s.trim());
			el.style[key] = value;
		});
	};

	function handleEscape(keyboardEvent, interactEvent) {
		if (keyboardEvent.code === 'Escape') {
			elemToMove.classList.add('escape');
			interactEvent.interaction.end();
			// After ESC press, disable text selection until mouse release
			toggleUserSelect('none');
			document.body.addEventListener(
				'mouseup',
				(mouseupListener = () => {
					toggleUserSelect(''); // Remove the explicitly added style
					document.body.removeEventListener('mouseup', mouseupListener);
				})
			);
		}
	}

	function toggleUserSelect(value, element = document.body) {
		element.style.userSelect = value;
		element.style.webkitUserSelect = value; // Need to add explicitly for Safari
	}

	const updateScrollParent = (newScrollParent) => {
		currentScrollParent = newScrollParent;
		interactjs({
			autoScroll: {
				container: newScrollParent // tell interactjs to scroll on this element
			}
		});
	};

	const interactjs = (options) => {
		interact(el).draggable(options);
	};

	interactjs({
		allowFrom: options.allow,
		ignoreFrom: options.ignore,
		listeners
	});
};

export default {
	name: 'draggable',
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: (args) => {
			const { host, action } = args;
			draggable(host, action.payload.options);
		}
	}
};
