import {
	COLLAPSE_ALL_SWIMLANES,
	EXPAND_ALL_SWIMLANES,
	NOW_VISUAL_BOARD_SWIMLANE_MOVED,
	TOGGLE_SWIMLANE_BODY,
	SWIMLANE_HEADER_UPDATED,
	VTB_SWIMLANE_MOVED,
	VTB_LANE_HEADER_UPDATED,
	VTB_SWIMLANE_ACTION_PERFORMED
} from '../../constants';

export default {
	[COLLAPSE_ALL_SWIMLANES]: ({ dispatch, action }) => {
		dispatch(VTB_SWIMLANE_ACTION_PERFORMED, {
			type: 'COLLAPSE_ALL_SWIMLANES'
		});
		action.stopPropagation();
	},
	[EXPAND_ALL_SWIMLANES]: ({ dispatch, action }) => {
		dispatch(VTB_SWIMLANE_ACTION_PERFORMED, {
			type: 'EXPAND_ALL_SWIMLANES'
		});
		action.stopPropagation();
	},
	[TOGGLE_SWIMLANE_BODY]: ({ dispatch, action }) => {
		dispatch(VTB_SWIMLANE_ACTION_PERFORMED, {
			payload: action.payload,
			type: 'TOGGLE_SWIMLANE_BODY'
		});
		action.stopPropagation();
	},
	[NOW_VISUAL_BOARD_SWIMLANE_MOVED]: ({ action, dispatch }) => {
		const { payload } = action;
		dispatch(VTB_SWIMLANE_MOVED, payload);
		action.stopPropagation();
	},
	[SWIMLANE_HEADER_UPDATED]: ({ action, dispatch }) => {
		let {
			payload: { value: newValue, id }
		} = action;
		newValue = newValue.trim();
		dispatch(VTB_LANE_HEADER_UPDATED, {
			name: newValue,
			laneId: id
		});
		action.stopPropagation();
	}
};
