import { createCustomElement } from '@servicenow/ui-core';
import { getTruncatedSla, getSlaColor } from '../../shared/sla-utils';
import '@servicenow/now-label-value';
import '@servicenow/now-highlighted-value';
import styles from './sn-vtb-card-info-inline.scss';
import If from '../../../shared/if';
import '@servicenow/now-tooltip';
import { t } from 'sn-translate';

const view = (state, { updateState }) => {
	const setTargetRef = (ref, elm) => {
		updateState({
			path: 'targetRef.' + ref,
			value: elm,
			operation: 'set'
		});
	};
	const { sla: slaTargetRef, dueDate: dueDateTargetRef, checklist: checklistTargetRef } = state.targetRef;
	const card = state.properties.data;
	const { checklist, sla, due_date_display_value, options } = card;
	const { due_date_field, show_sla, card_type } = options;
	const checklistItems = checklist ? checklist.items : [];
	const completedChecklist = checklistItems.filter((item) => item.complete).length;
	const dueDate = card.record[due_date_field] ? new Date(card.record[due_date_field].display_value) : '';

	function getChecklistColor(completedChecklist) {
		if (completedChecklist < checklistItems.length) return 'low';
		else return 'positive';
	}

	//Will be used later for localization of time across the world
	/*function getLocalDate(date){
		var tzOffset = window.g_tz_offset ? window.g_tz_offset : -(new Date().getTimezoneOffset() * 60000);
		var localDate = new Date(date).getTime() + tzOffset;
		// Safari has issues parsing '-' in date.
		if (isNaN(localDate)) {
			localDate = new Date(date.replace(/-/g, '/')).getTime() + tzOffset;
		}
		return new Date(localDate);
	}*/

	function getDueDateColor(dueDate) {
		if (!dueDate) return;
		const currDate = new Date();
		const dayBeforeDue = new Date(dueDate);
		dayBeforeDue.setDate(dueDate.getDate() - 1); //24 hours before dueDate
		if (currDate < dayBeforeDue) return 'low';
		else if (currDate > dayBeforeDue && currDate < dueDate) return 'high';
		else return 'critical';
	}

	return (
		<div className="sn-vtb-card-details-inline">
			<If test={sla && sla.percentage && show_sla}>
				<div className="sla inline-item">
					<div>
						<If test={card_type !== 'compact'}>
							<now-label-value-inline label={t('SLA')} />
						</If>
					</div>
					<div className="sla-value">
						<now-highlighted-value
							label={getTruncatedSla(sla)}
							icon="calendar-clock-outline"
							status={getSlaColor(sla)}
							hook-insert={({ elm }) => setTargetRef('sla', elm)}
							hook-destroy={() => {
								setTargetRef('sla', null);
							}}
						/>
					</div>
					<now-tooltip
						target-ref={slaTargetRef}
						content={t('SLA') + ': ' + (sla ? sla.percentage : '') + '%'}
					/>
				</div>
			</If>
			<If test={due_date_display_value}>
				<div className="due-date inline-item">
					<div>
						<If test={card_type !== 'compact'}>
							<now-label-value-inline label={t('Due Date')} />
						</If>
					</div>
					<div className="duedate-value">
						<now-highlighted-value
							label={due_date_display_value}
							icon="clock-outline"
							status={getDueDateColor(dueDate)}
							hook-insert={({ elm }) => setTargetRef('dueDate', elm)}
							hook-destroy={() => setTargetRef('dueDate', null)}
						/>
					</div>
					<now-tooltip
						target-ref={dueDateTargetRef}
						content={t('Due Date') + ': ' + due_date_display_value}
					/>
				</div>
			</If>
			<If test={checklistItems.length > 0}>
				<div className="checklist inline-item">
					<div>
						<If test={card_type !== 'compact'}>
							<now-label-value-inline label={t('Checklist')} />
						</If>
					</div>
					<div className="checkbox-value">
						<now-highlighted-value
							label={completedChecklist + ' / ' + checklistItems.length}
							icon="checkbox-checked-outline"
							status={getChecklistColor(completedChecklist)}
							hook-insert={({ elm }) => setTargetRef('checklist', elm)}
							hook-destroy={() => {
								setTargetRef('checklist', null);
							}}
						/>
					</div>
					<now-tooltip
						target-ref={checklistTargetRef}
						content={t('Checklist') + ': ' + completedChecklist + ' / ' + checklistItems.length}
					/>
				</div>
			</If>
		</div>
	);
};

createCustomElement('sn-vtb-card-info-inline', {
	view,
	properties: {
		data: { default: {} }
	},
	initialState: {
		targetRef: { default: {} }
	},
	styles
});
