import {
	RESPONDER_VTB_BOARD,
	RESPONDER_VTB_CHECKLIST_ITEM,
	RESPONDER_VTB_LANE,
	RESPONDER_VTB_CARD,
	RESPONDER_VTB_TASK,
	RESPONDER_VTB_MEMBER,
	RESPONDER_VTB_BOARD_LABEL,
	RESPONDER_VTB_LABEL_ENTRY
} from './amb-constants';
import { subscriptionActionHandlers } from './vtb-subscription-handlers';
import { VTB_AMB_MESSAGE_RECEIVED } from '../../actions/constants';

const queue = [];

export const ambActionHandlers = {
	[RESPONDER_VTB_BOARD]: ambResponder(RESPONDER_VTB_BOARD),
	[RESPONDER_VTB_TASK]: ambResponder(RESPONDER_VTB_TASK),
	[RESPONDER_VTB_CARD]: ambResponder(RESPONDER_VTB_CARD),
	[RESPONDER_VTB_LANE]: ambResponder(RESPONDER_VTB_LANE),
	[RESPONDER_VTB_MEMBER]: ambResponder(RESPONDER_VTB_MEMBER),
	[RESPONDER_VTB_BOARD_LABEL]: ambResponder(RESPONDER_VTB_BOARD_LABEL),
	[RESPONDER_VTB_LABEL_ENTRY]: ambResponder(RESPONDER_VTB_LABEL_ENTRY),
	[RESPONDER_VTB_CHECKLIST_ITEM]: ambResponder(RESPONDER_VTB_CHECKLIST_ITEM),

	...subscriptionActionHandlers
};
setInterval(() => {
	if (queue.length > 0) {
		const firstElement = queue.shift();
		/**
		 * This is temporary fix for an issue when a card is reordered via AMB in another session.
		 * In such cases, the card is being deleted
		 * This happens due to upadteState does not fire correct events with payload
		 * and end up sending card details as '[object Object]' in sn-vtb-card.js.
		 * todo find proper fix
		 *
		 */
		if (firstElement.payload.message && firstElement.payload.message.action === 'reorder_cards') {
			firstElement.dispatch(VTB_AMB_MESSAGE_RECEIVED, {
				type: firstElement.type,
				...firstElement.payload,
				action: 'removeReorderedCards'
			});
			setTimeout(() => {
				dispatchAMBUpdate(firstElement);
			}, 10);
		} else {
			dispatchAMBUpdate(firstElement);
		}
	}
}, 5);

function dispatchAMBUpdate(ambUpdate) {
	ambUpdate.dispatch(VTB_AMB_MESSAGE_RECEIVED, {
		type: ambUpdate.type,
		...ambUpdate.payload
	});
}

function ambResponder(type) {
	return function ({ dispatch, action }) {
		queue.push({
			type: type,
			payload: action.payload,
			dispatch: dispatch
		});
		action.stopPropagation();
	};
}
