import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import styles from './sn-vtb-lane-header.scss';
import { DELETE, CANCEL, ARCHIVE, LANE_ACTIONS } from '../../../constants/translated';
import { querySelectorDeep } from '@devsnc/library-vtb-commons';
import {
	ADD_CARD_CLICKED,
	DROPDOWN_ITEM_CLICKED,
	OPEN_ARCHIVED_ALL_CARDS_BY_LANE_MODAL,
	GUIDED,
	OPEN_CONFIRM_DELETE_LANE_MODAL,
	FREEFORM,
	LANE_HIDE_SELECTED,
	OPEN_LANE_MOVE_MODAL
} from '../../../actions/constants';
import { getLaneActionItems } from './lane-actions-config';
import '../shared/sn-inline-edit/sn-inline-edit';
import '@servicenow/now-dropdown';
import '@servicenow/now-modal';
import { t } from 'sn-translate';
const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

const view = (state) => {
	const {
		properties: { data: lane }
	} = state;
	const { name, id, options, editing } = lane;
	const boardType = options.board_type;
	return (
		<div className="sn-vtb-lane-header" aria-label={name} tabIndex="-1" role="columnheader">
			<sn-inline-edit
				className="lane-title"
				value={name}
				name="VTB_LANE_HEADER"
				tooltipContent={name}
				edit-on-focus={false}
				can-edit={boardType !== GUIDED}
				append-to-payload={{ id }}
				editing={editing}
				config-aria={{
					label: {
						ariaLabel: t('Lane: {0}', lane.name) + t(boardType !== GUIDED ? ', press Enter to modify' : '')
					}
				}}
			/>
			<span className="card-count">{lane.cardsCount}</span>
			<div className="lane-options">
				<now-dropdown
					variant="secondary"
					select="none"
					items={getLaneActionItems(lane)}
					icon="ellipsis-v-fill"
					tooltip-content={LANE_ACTIONS}
					config-aria={{ 'aria-label': LANE_ACTIONS }}
					hide-caret="true"
					manage-selected-items="true"
					bare="true"
				/>
			</div>
		</div>
	);
};

/**
 * @seismicElement sn-vtb-lane-header
 * @summary Component to be rendered in the lane header slot.
 */
export default createCustomElement('sn-vtb-lane-header', {
	view,
	properties: {
		/**
		 * lane data object
		 * @type {Object}
		 */
		data: { default: {} }
	},
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: ({ properties, host }) => {
			const { data: lane } = properties;
			const { isFocus } = lane;

			if (isFocus) {
				const focusableElem = querySelectorDeep('.sn-vtb-lane-header', host);
				focusableElem.focus();
			}
		},
		[DROPDOWN_ITEM_CLICKED]: ({ properties, action, dispatch }) => {
			const id = action.payload.item.id;
			const {
				data: { sys_id: laneId, board_id, name, options }
			} = properties;
			let boardType = null;
			switch (id) {
				case 'hideLane':
					dispatch(LANE_HIDE_SELECTED, { laneId: laneId });
					break;
				case 'deleteLane':
					boardType = options.board_type;
					let deleteModalContent = t('Are you sure you want to delete {0}?', name);
					if (boardType === FREEFORM) deleteModalContent += t(' Any cards in the lane will be archived.');
					const modalData = {
						headerLabel: t('Delete Lane'),
						content: deleteModalContent,
						footerActions: [
							{ label: DELETE, variant: 'primary-negative', id: 'deleteLane' },
							{ label: CANCEL, variant: 'secondary', id: 'cancel' }
						]
					};
					dispatch(OPEN_CONFIRM_DELETE_LANE_MODAL, {
						laneId: laneId,
						boardId: board_id,
						modalData
					});
					break;
				case 'addCard':
					boardType = options.board_type;
					const newCard = {
						lane_id: laneId,
						removed: false,
						position: 'top'
					};

					dispatch(ADD_CARD_CLICKED, { card: newCard });
					break;
				case 'archiveAllCards':
					let archiveAllCardsModalContent = t(
						'Are you sure you want to archive all cards in lane {0}?',
						name
					);

					const archiveAllCardsModalData = {
						headerLabel: t('Confirm Archive'),
						content: archiveAllCardsModalContent,
						footerActions: [
							{ label: ARCHIVE, variant: 'primary-negative', id: 'archiveAllCards' },
							{ label: CANCEL, variant: 'secondary', id: 'cancel' }
						]
					};
					dispatch(OPEN_ARCHIVED_ALL_CARDS_BY_LANE_MODAL, {
						laneId: laneId,
						modalData: archiveAllCardsModalData
					});
					break;
				case 'moveLane':
					dispatch(OPEN_LANE_MOVE_MODAL, { id: laneId });
					break;
			}
		},
		[actionTypes.COMPONENT_DOM_READY]: ({ host, dispatch }) => {
			const headerEl = querySelectorDeep('.sn-vtb-lane-header', host);
			if (headerEl) dispatch('REGISTER_ARROW_NAVIGABLE_ELEMENT', headerEl);
		}
	},
	styles: [styles]
});
