import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-vtb-swimlane-dest-placeholder.scss';

/**
 * @seismicElement sn-vtb-swimlane-dest-placeholder
 * @summary Component to be shown as a placeholder for swimlane at the drop zone during the drag event.
 */
createCustomElement('sn-vtb-swimlane-dest-placeholder', {
	view: ({ properties }) => <div className="sl-dest-placeholder" style={{ width: properties.width + 'px' }} />,
	properties: {
		width: { default: null },
		height: { default: null }
	},
	styles
});
