import { createCustomElement } from '@servicenow/ui-core';
import styles from './label-color-icon.scss';
import draggable from '../../../../behaviors/draggable';
import { t } from 'sn-translate';
import If from '../../../shared/if';
import '@servicenow/now-tooltip';

function getBorderColor(colorHex) {
	let factor = 203;
	let result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(colorHex);
	let rgb = {
		r: Math.round((parseInt(result[1], 16) * factor) / 255),
		g: Math.round((parseInt(result[2], 16) * factor) / 255),
		b: Math.round((parseInt(result[3], 16) * factor) / 255)
	};
	return ['rgb(', rgb.r, ',', rgb.g, ',', rgb.b, ')'].join('');
}

const view = (state, { updateState }) => {
	const { label, size, color, value, iconType } = state.properties;
	const setTargetRef = (ref) => updateState({ targetRef: ref });
	return (
		<div style={{ lineHeight: '1px' }}>
			<If test={iconType === 'div'}>
				<div
					hook-insert={({ elm }) => setTargetRef(elm)}
					hook-destroy={() => {
						setTargetRef(null);
					}}
					className={'label-color-icon -' + size + ' active-' + label.active}
					style={{ backgroundColor: color, borderColor: getBorderColor(color) }}
				>
					<now-tooltip target-ref={state.targetRef} content={value} />
				</div>
			</If>

			<If test={iconType === 'button'}>
				<button
					aria-label={t('{0}, update color', label.value)}
					aria-haspopup="true"
					className={'label-color-icon -' + size + ' active-' + label.active}
					style={{ backgroundColor: color, borderColor: getBorderColor(color) }}
				/>
			</If>
		</div>
	);
};

createCustomElement('sn-vtb-label-color-icon', {
	view,
	properties: {
		//adding reflect for cloning the shadow while dragging
		label: { default: '' },
		color: { default: '', reflect: true },
		value: { default: '', reflect: true },
		size: { default: '', reflect: true },
		iconType: { default: 'div' }
	},
	behaviors: [
		{
			behavior: draggable,
			options: {
				ignore: '.card-label', //don't allow drag of label on the card
				allow: '.active-true',
				xScrollParent: '.lanes-container',
				yScrollParent: '.lane-body' //changes for 2D board
			}
		}
	],
	styles
});
