import { VTB_CONFIRMATION_MODAL_SELECTED } from '../../../actions/constants';

export function openModal(id) {
	return ({ action, dispatch }) => {
		dispatch(VTB_CONFIRMATION_MODAL_SELECTED, {
			modalData: action.payload,
			modalId: id
		});
	};
}

export function closeModal() {
	return ({ dispatch }) =>
		dispatch(VTB_CONFIRMATION_MODAL_SELECTED, {
			modalData: {},
			modalId: null
		});
}
