import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-vtb-swimlane-src-placeholder.scss';

/**
 * @seismicElement sn-vtb-swimlane-src-placeholder
 * @summary Component to be shown as a placeholder for source swimlane when drag event starts.
 */
createCustomElement('sn-vtb-swimlane-src-placeholder', {
	view: ({ properties }) => <div className="sl-src-placeholder" style={{ width: properties.width + 'px' }} />,
	properties: {
		width: { default: null },
		height: { default: null }
	},
	styles
});
