import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import styles from './sn-vtb-card.scss';
import { querySelectorDeep } from '@devsnc/library-vtb-commons';
import '@servicenow/now-card';
import '@servicenow/now-modal';
import './sn-vtb-card-footer/sn-vtb-card-footer';
import './sn-vtb-card-info-inline/sn-vtb-card-info-inline';
import droppable, { attachmentDrop } from './card-dropzone';
import { getCardActionItems } from './card-actions-config';
import { t } from 'sn-translate';
import {
	renderCardAttachments,
	renderCardInfo,
	renderCardDescription,
	renderLabels,
	renderAttachmentOverlay
} from './card-view-utils';
import {
	CARD_CLICKED,
	DROPDOWN_ITEM_CLICKED,
	OPEN_CONFIRM_ARCHIVE_CARD_MODAL,
	OPEN_CARD_MOVE_MODAL,
	OPEN_CARD_DETAILS
} from '../../../actions/constants';
import If from '../../shared/if';
const { COMPONENT_PROPERTY_CHANGED } = actionTypes;
import { CARD_ACTIONS } from '../../../constants/translated';

const view = (state, { updateState }) => {
	if (typeof state.properties.data === 'string') {
		return;
	}
	const {
		properties: { data: card },
		attachmentHovering
	} = state;
	const {
		options,
		labels,
		isFocus,
		orderPerLaneInSwimlane,
		countPerLaneInSwimlane,
		id: cardId,
		record: {
			short_description: { display_value: cardHeading },
			description: { display_value: description }
		}
	} = card;

	const { board_type: boardType, show_labels, card_type, show_card_info } = options;
	const setTargetRef = (ele) => setTimeout(() => updateState({ overflowLabelsEle: ele }), 200);

	return (
		<div
			className="sn-vtb-card"
			hook-insert={({ elm }) =>
				setTimeout(() => {
					isFocus && elm.focus();
				}, 100)
			}
		>
			<now-card
				size="sm"
				interaction="click"
				configAria={{
					'aria-label': t(`Card: ${orderPerLaneInSwimlane} out of ${countPerLaneInSwimlane}, ${cardHeading}`)
				}}
			>
				<div className="card-header-container">
					<now-card-header className="card-header" heading={{ label: cardHeading, size: 'sm', lines: 1 }} />
					<now-dropdown
						items={getCardActionItems(cardId, boardType)}
						icon="ellipsis-v-outline"
						tooltip-content={CARD_ACTIONS}
						size="sm"
						variant="secondary"
						select="none"
						bare
						hide-caret
						hide-label
						className="card-dropdown"
						config-aria={{
							trigger: { 'aria-label': CARD_ACTIONS }
						}}
					/>
				</div>
				<If test={attachmentHovering}>{renderAttachmentOverlay()}</If>
				<If test={show_labels}>{renderLabels(labels, 19, setTargetRef, state.overflowLabelsEle)}</If>
				<If test={card_type === 'classic' && description}>{renderCardDescription(description)}</If>

				{/*SLA, CheckList, Due date*/}
				<sn-vtb-card-info-inline data={card} />

				{renderCardAttachments(card)}

				<If test={show_card_info && card_type === 'classic'}> {renderCardInfo(card)} </If>

				<sn-vtb-card-footer data={card} />
			</now-card>
		</div>
	);
};

/**
 * @seismicElement sn-vtb-card
 * @summary Component to be rendered in the card slot.
 */
createCustomElement('sn-vtb-card', {
	view,
	actionHandlers: {
		[COMPONENT_PROPERTY_CHANGED]: ({ properties, host }) => {
			const { data: card } = properties;
			const { isFocus } = card;

			if (isFocus === true) {
				const focusableElem = querySelectorDeep('.now-card', host);
				focusableElem.focus();
			}
		},
		[DROPDOWN_ITEM_CLICKED]: ({ action, dispatch }) => {
			const { id: actionType, uuid } = action.payload.item;
			switch (actionType) {
				case 'archive_card':
					const modalData = {
						headerLabel: t('Confirm Archive'),
						content: t('Are you sure you want to archive this card?'),
						footerActions: [
							{ label: t('Archive'), variant: 'primary-negative', id: 'archiveCard' },
							{ label: t('Cancel'), variant: 'secondary', id: 'cancel' }
						]
					};
					dispatch(OPEN_CONFIRM_ARCHIVE_CARD_MODAL, {
						cardId: uuid,
						modalData: modalData
					});
					break;
				case 'move_card':
					dispatch(OPEN_CARD_MOVE_MODAL, {
						id: uuid
					});
					break;
			}
		},
		[CARD_CLICKED]: ({ state, action, dispatch }) => dispatch(OPEN_CARD_DETAILS, state.properties.data),
		[actionTypes.COMPONENT_DOM_TREE_READY]: ({ host, dispatch }) => {
			const navigableCardEl = querySelectorDeep('.now-card', host);
			if (navigableCardEl) dispatch('REGISTER_ARROW_NAVIGABLE_ELEMENT', navigableCardEl);
		}
	},
	properties: {
		/**
		 * Card data object
		 * @type {Object}
		 */
		data: {
			default: {},
			reflect: true // necessary to create proper clone of this element
		}
	},
	behaviors: [
		{
			behavior: droppable,
			options: {
				prefix: 'LABEL', //can be ASSIGNEE, ATTACHMENT
				allowFrom: 'label-color-icon'
			}
		},
		{
			behavior: attachmentDrop
		}
	],
	styles: [styles]
});
