import {
	COLLAPSE_ALL,
	EXPAND_ALL,
	MOVE_SWIMLANE,
	HIDE_SWIMLANE,
	DELETE_SWIMLANE,
	ARCHIVE_ALL_CARDS
} from '../../../constants/translated';
import { GUIDED, FLEXIBLE, FREEFORM } from '../../../actions/constants';

const hasDeleteOption = function (swimlane, options) {
	return !(
		(options.board_type === GUIDED && swimlane.value === '') ||
		(options.board_type === GUIDED && swimlane.cardsCount > 0) ||
		(options.board_type === GUIDED && swimlane.is_choice_lane) ||
		(options.board_type === FLEXIBLE && swimlane.cardsCount > 0) ||
		(swimlane.is_reference_lane && options.swimlane_filter)
	);
};

export function getSwimlaneActionItems(swimlane, collapsed) {
	const { id: swimlaneId, options, cardsCount } = swimlane;
	const swimlaneDropdownItems = [
		{ id: 'collapseAllSl', label: COLLAPSE_ALL, uuid: swimlaneId, disabled: collapsed },
		{ id: 'expandAllSl', label: EXPAND_ALL, uuid: swimlaneId, disabled: !collapsed },
		{ id: 'hideSwimlane', label: HIDE_SWIMLANE, uuid: swimlaneId },
		{ id: 'moveSwimlane', label: MOVE_SWIMLANE, uuid: swimlaneId }
	];
	if (hasDeleteOption(swimlane, options)) {
		swimlaneDropdownItems.push({ id: 'deleteSwimlane', label: DELETE_SWIMLANE, uuid: swimlaneId });
	}
	if (options.board_type === FREEFORM && cardsCount > 0) {
		swimlaneDropdownItems.push({ id: 'archiveAllCards', label: ARCHIVE_ALL_CARDS, uuid: swimlaneId });
	}
	return swimlaneDropdownItems;
}
