import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-vtb-card-src-placeholder.scss';

/**
 * @seismicElement sn-vtb-card-src-placeholder
 * @summary Component to be shown as a placeholder for source card when drag event starts.
 */
createCustomElement('sn-vtb-card-src-placeholder', {
	view: ({ properties }) => (
		<div
			style={{
				height: `${properties.height}px`,
				width: `${properties.width}px`
			}}
		>
			<div className="sn-vtb-card-src-placeholder" />
		</div>
	),
	properties: {
		width: { default: 0 },
		height: { default: 0 }
	},
	styles
});
