import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-inline-edit.scss';
import If from '../../../shared/if';
import {
	SET_EDITABLE,
	ON_LABEL_FOCUS,
	MODE_CHANGED,
	KEY_ENTER,
	KEY_ESCAPE,
	ACTION_PREFIX,
	ON_LABEL_CLICK,
	ON_INPUT_BLUR,
	VALUE_EDITING,
	VALUE_UPDATED,
	ON_INPUT_KEYPRESS,
	VALUE_UPDATE_CANCELLED
} from './constants';
import get from 'lodash/get';
import '@servicenow/now-tooltip';

function inputMode(editedValue, properties, dispatch) {
	const { value, canEdit, placeholder, configAria, styles } = properties;
	// TODO: Add multi-line support (using textarea)
	return (
		<input
			className="value-input"
			value={editedValue != null ? editedValue : value}
			placeholder={placeholder}
			editable={canEdit}
			hook-insert={
				// select the entire text, and scroll the input into view
				({ elm }) => {
					elm.select();
					elm.scrollIntoView({ behavior: 'smooth', block: 'nearest', inline: 'nearest' });
				}
			}
			on-blur={(e) => dispatch(ON_INPUT_BLUR, { value: e.target.value })}
			on-keydown={(e) =>
				dispatch(ON_INPUT_KEYPRESS, {
					key: e.key,
					target: e.target
				})
			}
			aria-label={get(configAria, 'input.ariaLabel', '')}
			style={get(styles, 'input', null)}
		/>
	);
}

function labelMode(focusLabel, properties, dispatch) {
	const { value, styles, configAria } = properties;
	return (
		<label
			className="value-label"
			tab-index="0"
			hook-insert={({ elm }) => focusLabel && elm.focus()}
			on-focus={() => dispatch(ON_LABEL_FOCUS)}
			on-click={() => dispatch(ON_LABEL_CLICK)}
			on-keydown={(e) => e.key === KEY_ENTER && dispatch(ON_LABEL_CLICK)}
			aria-label={get(configAria, 'label.ariaLabel', '')}
			style={get(styles, 'label', null)}
		>
			{value}
		</label>
	);
}

const view = (state, { dispatch, updateState }) => {
	const setTargetRef = (ref) => updateState({ targetRef: ref });
	const { properties, editing, editedValue, focusLabel } = state;
	const isEditing = properties.editing || editing;
	return (
		<div
			className={`sn-inline-edit ${properties.name}`}
			hook-insert={({ elm }) => setTargetRef(elm)}
			hook-destroy={() => {
				setTargetRef(null);
			}}
		>
			<If test={isEditing}>{inputMode(editedValue, properties, dispatch)}</If>
			<If test={!isEditing}>{labelMode(focusLabel, properties, dispatch)}</If>
			<now-tooltip target-ref={state.targetRef} content={properties.tooltipContent} />
		</div>
	);
};

/**
 * A component that can show a value in the form of a label, and allows editing that value in-place by switching
 * to a input component when the user wants to edit it.
 *
 * @author mohammed.furqan
 *
 * */
createCustomElement('sn-inline-edit', {
	view,
	initialState: {
		focusLabel: false,
		editing: false,
		editedValue: null
	},
	properties: {
		/**
		 * A unique name to identify this instance of the inline edit component. The name will also be used as
		 * the prefix for all the actions that this component dispatches
		 */
		name: {
			required: false,
			schema: { type: 'string' },
			default: ''
		},

		/**
		 * The value that this component will display initially.
		 */
		value: {
			required: true,
			schema: { type: 'string' },
			default: ''
		},

		/**
		 * Tooltip content to be displayed
		 */
		tooltipContent: {
			required: false,
			schema: { type: 'string' },
			default: ''
		},

		/**
		 * A boolean indicating whether the value can be edited. Setting it to false will always keep the
		 * component in the 'label' mode (non-editable)
		 *
		 */
		canEdit: {
			required: false,
			schema: { type: 'boolean' },
			default: true
		},

		/**
		 * A boolean indicating whether the component should change to an editable mode when the value is
		 * focussed. If true, the value label will change into an editable input box as soon as it receives
		 * the focus.
		 */
		editOnFocus: {
			required: false,
			schema: { type: 'boolean' },
			default: false
		},

		/**
		 * A boolean indicating whether the component should save the new value when the editable input box
		 * is blurred, either by clicking somewhere outside, or by tabbing out. The component will dispatch
		 * an action indicating the save event.
		 */
		saveOnBlur: {
			required: false,
			schema: { type: 'boolean' },
			default: true
		},

		/**
		 * A boolean indicating whether the component is in the editing mode initially. By default, the component
		 * is in "display" mode, but it can be set to "editing" mode using this property.
		 */
		editing: {
			required: false,
			schema: { type: 'boolean' },
			default: false
		},

		/**
		 * Placeholder for the input field when the component is in editing mode
		 */
		placeholder: {
			required: false,
			schema: { type: 'string' },
			default: ''
		},

		/**
		 *
		 * An object containing the aria labels that needs to be applied on the 'label' and the 'input' element
		 * inside this component.
		 */
		configAria: {
			required: false,
			schema: {
				type: 'object',
				properties: {
					label: {
						type: 'object',
						properties: {
							ariaLabel: { type: 'string' }
						}
					},
					input: {
						type: 'object',
						properties: {
							ariaLabel: { type: 'string' }
						}
					}
				},
				additionalProperties: false
			},
			default: {}
		},

		/**
		 *
		 * An object containing the styles that need to be applied to the inner elements like the 'label' and the
		 * 'input' element.
		 */
		styles: {
			required: false,
			schema: {
				type: 'object',
				properties: {
					label: {
						type: 'object'
					},
					input: {
						type: 'object'
					}
				},
				additionalProperties: false
			},
			default: {}
		}
	},
	actionHandlers: {
		[ON_INPUT_BLUR]: ({ state, action, updateState, dispatch }) => {
			const {
				properties: { name, value, saveOnBlur }
			} = state;
			const actionPrefix = `${name || ACTION_PREFIX}#`;
			if (saveOnBlur) {
				dispatch(actionPrefix + VALUE_UPDATED, {
					newValue: action.payload.value,
					previousValue: value
				});
				updateState({
					editing: false,
					editedValue: null
				});
			} else {
				dispatch(actionPrefix + VALUE_UPDATE_CANCELLED, {
					value
				});
				updateState({
					focusLabel: true,
					editing: false,
					editedValue: null
				});
			}
		},
		[ON_INPUT_KEYPRESS]: ({ state, action, dispatch, updateState }) => {
			const { key, target } = action.payload;
			const inputValue = target.value;
			const {
				properties: { name, value }
			} = state;
			const actionPrefix = `${name || ACTION_PREFIX}#`;
			switch (key) {
				case KEY_ENTER:
					// Save the value
					updateState({
						focusLabel: true,
						editing: false,
						editedValue: null
					});
					dispatch(actionPrefix + VALUE_UPDATED, {
						newValue: inputValue,
						previousValue: value
					});
					break;
				case KEY_ESCAPE:
					// Revert to the previous value
					updateState({
						focusLabel: true,
						editing: false,
						editedValue: null
					});
					dispatch(actionPrefix + VALUE_UPDATE_CANCELLED, {
						value
					});
					break;
				default:
					updateState({
						editedValue: inputValue
					});
					dispatch(actionPrefix + VALUE_EDITING, {
						newValue: inputValue,
						previousValue: value
					});
					break;
			}
		},
		[ON_LABEL_FOCUS]: ({ state, dispatch }) => {
			const {
				properties: { canEdit, editOnFocus }
			} = state;
			if (editOnFocus && canEdit) dispatch(SET_EDITABLE, { editing: true });
		},
		[ON_LABEL_CLICK]: ({ state, dispatch }) => {
			const {
				properties: { canEdit }
			} = state;
			if (canEdit) dispatch(SET_EDITABLE, { editing: true });
		},
		[SET_EDITABLE]: ({ state, action, dispatch, updateState }) => {
			const modifiedState = { editing: action.payload.editing };
			updateState(modifiedState);

			const {
				properties: { name }
			} = state;
			const actionPrefix = `${name || ACTION_PREFIX}#`;
			dispatch(actionPrefix + MODE_CHANGED, {
				modifiedState
			});
		}
	},
	styles: [styles],
	dispatches: {
		/**
		 * Dispatched when the `editing` property is set
		 * @type {{editing: boolean}}
		 */
		'SN_INLINE_EDIT#MODE_CHANGED': {
			schema: {
				type: 'object',
				properties: {
					editing: { type: 'boolean' }
				}
			}
		},
		/**
		 * Dispatched when value is updated in the following conditions
		 * 	1. on-blur if save-on-blur is active
		 * 	2. on pressing 'Enter' key
		 * 	@type {{newValue: string, previousValue: string}}
		 */
		'SN_INLINE_EDIT#VALUE_UPDATED': {
			schema: {
				type: 'object',
				properties: {
					newValue: { type: 'string' },
					previousValue: { type: 'string' }
				}
			}
		},
		/**
		 * Dispatched when value is being updated (on each key press). Useful for type-ahead texts
		 * @type {{newValue: string, previousValue: string}}
		 */
		'SN_INLINE_EDIT#VALUE_EDITING': {
			schema: {
				type: 'object',
				properties: {
					newValue: { type: 'string' },
					previousValue: { type: 'string' }
				}
			}
		},
		/**
		 * Dispatched when 'Escape' key is pressed or when the editing is cancelled.
		 * @type {{newValue: string, previousValue: string}}
		 */
		'SN_INLINE_EDIT#VALUE_UPDATE_CANCELLED': {
			schema: {
				type: 'object',
				properties: {
					newValue: { type: 'string' },
					previousValue: { type: 'string' }
				}
			}
		}
	}
});
