import {
	MODAL_OPENED_SET,
	OPEN_CARD_DETAILS,
	OPEN_CONFIRM_ARCHIVE_CARD_MODAL,
	OPEN_ARCHIVED_ALL_CARDS_BY_LANE_MODAL,
	OPEN_CONFIRM_DELETE_LANE_MODAL
} from '../../actions/constants';
import { openModal, closeModal } from '../board/sn-vtb-modal/modal-utils';
import cardActionHandlers from '../../actions/handlers/card-handlers/card-handlers';
import laneActionHandlers from '../../actions/handlers/lane-handlers/lane-handlers';
import swimlaneActionHandlers from '../../actions/handlers/swimlane-handlers/swimlane-handlers';
import { ambActionHandlers } from '../amb/amb-action-handlers';
import { actionTypes } from '@servicenow/ui-core';
import { BOARD_ID } from '../../components/shared/constants';
import { AMB_SUBSCRIPTION_INIT } from '../amb/amb-constants';
import { accessibilityKeyHandlers } from '../../actions/handlers/a11y-key-handler';
const { COMPONENT_PROPERTY_CHANGED, COMPONENT_ERROR_THROWN } = actionTypes;

export default {
	[OPEN_CARD_DETAILS]: openModal('cardDetails'),
	[MODAL_OPENED_SET]: closeModal(),
	[OPEN_CONFIRM_ARCHIVE_CARD_MODAL]: openModal('archiveCard'),
	[OPEN_CONFIRM_DELETE_LANE_MODAL]: openModal('deleteLane'),
	[OPEN_ARCHIVED_ALL_CARDS_BY_LANE_MODAL]: openModal('archiveAllCards'),
	[COMPONENT_PROPERTY_CHANGED]: ({ action, dispatch }) => {
		const { name, previousValue, value } = action.payload;

		// AMB subscription should happen only once, when the boardId property changed for the first time.
		if (name === BOARD_ID && !previousValue && value) {
			dispatch(AMB_SUBSCRIPTION_INIT, {
				boardId: value,
				subscribe: true
			});
		}
	},
	[COMPONENT_ERROR_THROWN]: ({ action }) => {
		console.error('@sn-vtb/sn-vtb-container ', action.payload);
	},
	...cardActionHandlers,
	...laneActionHandlers,
	...swimlaneActionHandlers,
	...ambActionHandlers,
	...accessibilityKeyHandlers
};
