import {
	FREEFORM_CARD_ADDED,
	REMOVE_FREEFORM_PLACEHOLDER_CARD,
	VTB_FREEFORM_PLACEHOLDER_CARD_REMOVED,
	VTB_FREEFORM_CARD_ADDED,
	ADD_CARD_CLICKED,
	VTB_ADD_CARD_CLICKED
} from '../../constants';

export default {
	[ADD_CARD_CLICKED]: ({ action, dispatch }) => {
		const { payload } = action;
		dispatch(VTB_ADD_CARD_CLICKED, payload);
		action.stopPropagation();
	},
	[REMOVE_FREEFORM_PLACEHOLDER_CARD]: ({ dispatch, action }) => {
		const { payload } = action;
		dispatch(VTB_FREEFORM_PLACEHOLDER_CARD_REMOVED, payload);
		action.stopPropagation();
	},
	[FREEFORM_CARD_ADDED]: ({ action, dispatch }) => {
		const { payload } = action;
		dispatch(VTB_FREEFORM_CARD_ADDED, payload);
		action.stopPropagation();
	}
};
