import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-card';
import '@servicenow/now-button';
import '@servicenow/now-record-field-lookup-connected';
import '../../shared/sn-inline-edit/sn-inline-edit';
import If from '../../../shared/if';
import styles from './sn-vtb-add-lane.scss';
import '../../shared/sn-vtb-reference-picker/sn-vtb-reference-picker';
import {
	BUTTON_CLICKED,
	LANE_CREATE_REQUEST,
	NOW_RECORD_FIELD_VALUE_CHANGED,
	GUIDED,
	VTB_ADD_LANE_REFERENCE_PICKER_BLUR
} from '../../../../actions/constants';
import { ADD_LANE } from '../../../../constants/translated';
import { t } from 'sn-translate';
import { querySelectorDeep } from '@devsnc/library-vtb-commons';
import '@servicenow/now-tooltip';

const view = (state, { dispatch, updateState }) => {
	const { showReferencePicker } = state;
	const {
		properties: { data: lane }
	} = state;
	const setTargetRef = (ref) => updateState({ targetRef: ref });
	const { options, lanes_reference_table_records } = lane;
	const { reference_lane_search_field, field_reference_table } = options;
	const addNewReferenceLaneQuery =
		reference_lane_search_field +
		'STARTSWITH^' +
		reference_lane_search_field +
		'ISNOTEMPTY^sys_idNOT IN' +
		lanes_reference_table_records;

	return (
		<div className="lane-add">
			<div className={showReferencePicker ? 'lane-add-btn-container-hide' : 'lane-add-btn-container'}>
				<button
					className="lane-add-btn"
					on-click={() => dispatch(BUTTON_CLICKED)}
					aria-label={ADD_LANE}
					hook-insert={({ elm }) => setTargetRef(elm)}
					hook-destroy={() => setTargetRef(null)}
				>
					<div className="lane-add-btn-content">
						<now-icon className="lane-add-btn-icon" icon="plus-outline" aria-hidden="true" />
						<span className="lane-add-btn-label">{ADD_LANE}</span>
					</div>
				</button>
				<now-tooltip target-ref={state.targetRef} content={ADD_LANE} />
			</div>

			<If test={showReferencePicker && options.board_type === GUIDED}>
				<div className="lane-add-reference-picker">
					<sn-vtb-reference-picker
						name="VTB_ADD_LANE"
						tableName={field_reference_table}
						fieldName={reference_lane_search_field}
						aria-label={ADD_LANE}
						placeholder={t('Add New Lane')}
						autofocus={true}
						additionalQueryString={addNewReferenceLaneQuery}
					/>
				</div>
			</If>
		</div>
	);
};

/**
 * @seismicElement sn-vtb-add-lane
 * @summary Component to be rendered in the add lane slot.
 */
export default createCustomElement('sn-vtb-add-lane', {
	view,
	properties: {
		/**
		 * lane data object
		 * @type {Object}
		 */
		data: {
			default: {}
		}
	},
	actionHandlers: {
		[BUTTON_CLICKED]: ({ dispatch, updateState, properties }) => {
			const {
				data: { board_id, options }
			} = properties;
			if (options.board_type === GUIDED) {
				updateState({
					path: 'showReferencePicker',
					value: true,
					operation: 'set'
				});
			} else {
				const payload = {
					createLaneInput: {
						boardId: board_id,
						value: '',
						name: 'Lane #' + (options.lane_count + 1),
						order: options.highest_lane_order + 1,
						isSwimLane: false
					}
				};
				dispatch(LANE_CREATE_REQUEST, payload.createLaneInput);
			}
		},
		[NOW_RECORD_FIELD_VALUE_CHANGED]: ({ action, dispatch, properties, updateState }) => {
			const {
				data: { board_id, options }
			} = properties;
			if (action.payload.value && action.payload.value !== '') {
				const payload = {
					createLaneInput: {
						boardId: board_id,
						value: action.payload.value,
						name: action.payload.displayValue,
						order: options.highest_lane_order + 1,
						isSwimLane: false
					}
				};
				dispatch(LANE_CREATE_REQUEST, payload.createLaneInput);
				updateState({
					path: 'showReferencePicker',
					value: false,
					operation: 'set'
				});
			}
		},
		//On input blur when adding reference lane
		[VTB_ADD_LANE_REFERENCE_PICKER_BLUR]: ({ host, updateState }) => {
			updateState({
				path: 'showReferencePicker',
				value: false,
				operation: 'set'
			});
			setTimeout(() => {
				querySelectorDeep('button.lane-add-btn', host).focus();
			}, 100);
		},
		[actionTypes.COMPONENT_DOM_READY]: ({ host, dispatch }) => {
			const buttonEl = querySelectorDeep('.lane-add-btn', host);
			if (buttonEl) dispatch('REGISTER_ARROW_NAVIGABLE_ELEMENT', buttonEl);
		}
	},
	styles,
	initialState: {
		showReferencePicker: false
	}
});
