import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { querySelectorDeep } from '@devsnc/library-vtb-commons';
import { BUTTON_CLICKED, ADD_CARD_CLICKED } from '../../../actions/constants';
import { ADD_CARD } from '../../../constants/translated';
import styles from './sn-vtb-lane-footer.scss';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import If from '../../shared/if';
import get from 'lodash/get';

const view = ({ properties }) => {
	const { data: lane, swimlane = {} } = properties;
	const {
		options: { can_add_task }
	} = lane;
	const toogleFooter = get(swimlane, `cardsCountPerLane.${lane.sys_id}`, 0) > 0;
	return (
		<footer className="lane-footer">
			<If test={can_add_task}>
				<div role="button" aria-label={ADD_CARD} className={`add-card ${toogleFooter && 'toggle-on-focus'}`}>
					<now-button
						label={'+ ' + ADD_CARD}
						size="sm"
						tooltip-content={ADD_CARD}
						variant="primary"
						config-aria={{ 'aria-label': ADD_CARD }}
					/>
				</div>
			</If>
		</footer>
	);
};

/**
 * @seismicElement sn-vtb-lane-footer
 * @summary Component to be rendered in the lane footer slot.
 */
export default createCustomElement('sn-vtb-lane-footer', {
	view,
	actionHandlers: {
		[BUTTON_CLICKED]: ({ properties, action, dispatch }) => {
			const {
				data: { sys_id, options },
				swimlane
			} = properties;

			const newCard = {
				lane_id: sys_id,
				position: 'bottom'
			};
			if (swimlane) newCard.swim_lane_id = swimlane.sys_id;
			dispatch(ADD_CARD_CLICKED, { card: newCard });
		},
		[actionTypes.COMPONENT_DOM_READY]: ({ host, dispatch }) => {
			const addCardEl = querySelectorDeep('.add-card', host);
			if (addCardEl) dispatch('REGISTER_ARROW_NAVIGABLE_ELEMENT', addCardEl);
		}
	},
	properties: {
		/**
		 * lane data object
		 * @type {Object}
		 */
		data: { default: {} },
		/**
		 * swimlane object
		 * @type {Object}
		 */
		swimlane: {
			default: {}
		}
	},
	styles
});
