import '@servicenow/now-icon';
import '@servicenow/now-image';
import '@servicenow/now-label-value';
import '../shared/label-color-icon/sn-vtb-label-color-icon';
import If from '../../shared/if';
import { t } from 'sn-translate';

export function renderCardAttachments(card) {
	const {
		attachments,
		thumbnail,
		options: { card_type: cardType, show_cover_image: attachmentPref }
	} = card;
	const showAttachment = cardType === 'classic' && attachmentPref && attachments.length > 0;
	const unavailableAttachments = attachments.filter((att) => att.state === 'not_available').length;
	return (
		<If test={showAttachment}>
			<div className="card-attachment">
				<div className="card-attachment-content">
					<now-icon className="card-attachment-icon" icon="paperclip-outline" size="sm" />
					<now-label-value-inline
						className="card-attachment-text"
						label="Attachments"
						value={
							unavailableAttachments > 0
								? `${attachments.length} (${unavailableAttachments} unavailable)`
								: attachments.length
						}
					/>
				</div>
				<If test={thumbnail}>
					<now-image className="card-attachment-image" alt="attachment" fit="fill" src={thumbnail} />
				</If>
			</div>
		</If>
	);
}

export function renderCardInfo(card) {
	const { record } = card;
	const FIELD_BLACKLIST = [
		'number',
		'short_description',
		'sys_updated_on',
		'description',
		'additional_assignee_list',
		'dirty',
		'unsaved'
	];

	let fieldItems = [];
	Object.values(record)
		.filter((field) => field.name && !FIELD_BLACKLIST.includes(field.name) && field.display_value)
		.forEach((field) => {
			let fieldItem = {};
			fieldItem.label = t(field.label);
			fieldItem.value = { type: 'string', value: field.display_value };
			fieldItems.push(fieldItem);
		});

	return (
		<div className="card-info">
			<now-label-value-tabbed items={fieldItems} delimiter="," size="md" />
		</div>
	);
}

export function renderCardDescription(description) {
	return <div className="card-description">{description}</div>;
}

export function renderLabels(labels, visibleLabelsCount, setTargetRef, overflowLabelsEle) {
	if (!labels) labels = [];

	const overflowLabels = labels.slice(visibleLabelsCount);
	const activeLabels = labels.slice(0, visibleLabelsCount);

	return (
		<div className="sn-vtb-card-labels">
			<If test={activeLabels}>
				<div className="labels-container">
					{activeLabels.map((label) => (
						<If test={label.active}>
							<sn-vtb-label-color-icon
								className="card-label"
								color={label.color}
								value={label.value}
								size="sm"
							/>
						</If>
					))}
					<If test={overflowLabels.length > 0}>
						<span
							hook-insert={({ elm }) => setTargetRef(elm)}
							hook-destroy={() => setTargetRef(null)}
							aria-describedby="overflow-labels-tooltip"
							className="overflow-labels-text"
						>{`+${overflowLabels.length}`}</span>
						<now-tooltip id="overflow-labels-tooltip" target-ref={overflowLabelsEle}>
							<div>
								{overflowLabels.map((label) => (
									<div>{label.value}</div>
								))}
							</div>
						</now-tooltip>
					</If>
				</div>
			</If>
		</div>
	);
}

export function renderLabelsOverlay() {
	return (
		<div className="vtb-dropzone-wrapper">
			<div className="vtb-dropzone vtb-dropzone_hover">
				<now-icon className="vtb-dropzone-icon" icon="tag-outline" size="xl" />
			</div>
		</div>
	);
}

export function renderAttachmentOverlay() {
	return (
		<div className="vtb-dropzone-wrapper">
			<div className="vtb-dropzone vtb-dropzone_hover">
				<now-icon className="vtb-dropzone-icon" icon="paperclip-outline" size="xl" />
			</div>
		</div>
	);
}
