import { createCustomElement } from '@servicenow/ui-core';
import { BUTTON_CLICKED, GFORM_SUBMIT, MODAL_OPENED_SET, ALERT_ACTION_CLICKED } from '../../../actions/constants';
import '@servicenow/now-button';
import '@servicenow/now-alert';
import styles from './sn-vtb-record-form-header.scss';
import { t } from 'sn-translate';
import If from '../../shared/if';

const view = (state) => {
	const { message } = state.properties;
	const { showAlert } = state;
	const buttonLabel = message ? t('Update') : t('Submit');
	console.log('message', message);
	return (
		<div className="record-header">
			<If test={showAlert && message !== ''}>
				<now-alert
					status="critical"
					icon="info-circle-outline"
					header=""
					content={message}
					textLinkProps={{}}
					action={{ type: 'dismiss' }}
				/>
			</If>
			<now-button label={buttonLabel} variant="primary" size="md" config-aria={{ 'aria-label': buttonLabel }} />
		</div>
	);
};
/**
 * @seismicElement sn-vtb-record-form-header
 * @summary Header Component for Add Card form modal.
 *
 */
createCustomElement('sn-vtb-record-form-header', {
	view,
	properties: { message: { default: '' } },
	initialState: {
		showAlert: true
	},
	actionHandlers: {
		/**
		 * Handles Submit of the form
		 * On button click, it submits GForm by dispatching GFORM_SUBMIT event
		 * @param dispatch GFORM_SUBMIT
		 */
		[BUTTON_CLICKED]: ({ dispatch }) => {
			dispatch(
				GFORM_SUBMIT,
				{ submitActionName: 'sysverb_ws_save' },
				{
					callback: (result) => {
						if (result) {
							result.then(() => {
								dispatch(MODAL_OPENED_SET);
							});
						}
					}
				}
			);
		},
		[ALERT_ACTION_CLICKED]: ({ updateState, state, payload }) => {
			updateState({
				showAlert: false
			});
		}
	},
	styles
});
