import { createCustomElement } from '@servicenow/ui-core';
import { VTB_CARD_MOVED, VTB_CREATE_LANE_REQUEST } from '../../actions/constants';
import actionHandlers from './actions';

const view = () => {
	return (
		<div className="vtb-connected">
			<slot name="vtb-container-slot" />
		</div>
	);
};

/**
 * @seismicElement sn-vtb-container
 * @summary container component for now-visual-board. it re dispatches the events generated by now-visual-board.
 * Also, it is an AMB connected component which is subscribed to AMB updates
 */
createCustomElement('sn-vtb-container', {
	view,
	properties: {
		/**
		 * Sys id of board.
		 * @type {string}
		 */
		boardId: {
			schema: {
				type: 'string'
			}
		}
	},
	dispatches: {
		[VTB_CARD_MOVED]: {},
		[VTB_CREATE_LANE_REQUEST]: {}
	},

	actionHandlers: actionHandlers,
	initialState: {
		cardMoveFailProps: {
			cardId: '',
			previousLane: '',
			previousSwimlane: '',
			previousOrder: ''
		}
	}
});
