import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-record-field-lookup-connected';
import styles from './sn-vtb-reference-picker.scss';
import { KeyCodes } from '../../../shared/constants';
import { querySelectorDeep } from '@devsnc/library-vtb-commons';

let referenceListClickListener, documentClickListener, tabListener;

let handleClick = (event, dispatch, name) => {
	if (event.target.tagName !== 'NOW-RECORD-FIELD-LOOKUP-CONNECTED') {
		dispatch(name + '#REFERENCE_PICKER_BLUR');
	} else {
		//if click is on reference dropdown list
		event.stopPropagation();
	}
};

let handleTabEsc = (event, dispatch, name) => {
	if (event.keyCode === KeyCodes.ESC || event.keyCode === KeyCodes.TAB) {
		dispatch(name + '#REFERENCE_PICKER_BLUR');
	}
};

const view = (state, { dispatch }) => {
	const { name } = state.properties;
	return (
		//There is no auto-reload in this component when scrolled down as in classic-vtb
		<div className="sn-vtb-reference-picker">
			<now-record-field-lookup-connected
				{...state.properties}
				hook-insert={({ elm }) => {
					elm.addEventListener('keydown', (tabListener = (e) => handleTabEsc(e, dispatch, name)));
					elm.addEventListener('click', (referenceListClickListener = (e) => handleClick(e, dispatch, name)));
					document.addEventListener('click', (documentClickListener = (e) => handleClick(e, dispatch, name)));
					//Todo : Remove below code after autofocus property got fixed (DEF0120493)
					setTimeout(() => {
						const el = querySelectorDeep('input', elm);
						if (el) {
							el.select();
							el.click();
						}
					}, 100);
				}}
				hook-destroy={({ elm }) => {
					document.removeEventListener('click', documentClickListener);
					elm.removeEventListener('click', referenceListClickListener);
					elm.removeEventListener('keydown', tabListener);
				}}
			/>
		</div>
	);
};

createCustomElement('sn-vtb-reference-picker', {
	view,
	properties: {
		name: { default: '' },
		tableName: { default: '' },
		fieldName: { default: '' },
		autofocus: { default: false },
		placeholder: { default: '' },
		ariaLabel: { default: '' },
		additionalQueryString: { default: '' }
	},
	styles
});
