import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-vtb-card-details-modal.scss';
import '@servicenow/now-modal';
import '@servicenow/now-record-form-connected';
import { t } from 'sn-translate';
import { CARD_DETAILS_MODAL_CLOSED, MODAL_OPENED_SET, SEISMIC_COMPONENT_RENDERED } from '../../../actions/constants';
import { querySelectorDeep } from '@devsnc/library-vtb-commons';

const view = (state) => {
	const { table, recordId, view, isVisible } = state.properties;

	if (!isVisible) return null;

	return (
		<now-modal size="lg" opened="true" header-label={t('Card Details')}>
			<div className="card-details">
				<now-record-form-connected sys-id={recordId} table={table} view={view} />
			</div>
		</now-modal>
	);
};

/**
 * @seismicElement sn-vtb-card-details-modal
 * @summary Component to display card details
 *
 */
createCustomElement('sn-vtb-card-details-modal', {
	view,

	properties: {
		/**
		 * Table name of the record to be displayed
		 * @type {String}
		 */
		table: {
			default: ''
		},
		/**
		 * SysId of the record to display
		 * @type {String}
		 */
		recordId: {
			default: -1
		},
		/**
		 * Record view
		 * @type {String}
		 */
		view: {
			default: 'workspace'
		},
		/**
		 * show/hide Card details modal
		 * @type {String}
		 */
		isVisible: {
			default: false
		}
	},
	actionHandlers: {
		[SEISMIC_COMPONENT_RENDERED]: ({ host }) => {
			setTimeout(() => {
				const dropdownEl = querySelectorDeep('.now-button-iconic', host);
				if (dropdownEl) dropdownEl.focus();
			}, 100);
		},
		/**
		 * On modal close, re-dispatches CARD_DETAILS_MODAL_CLOSED event
		 * @param action handles MODAL_OPENED_SET
		 * @param dispatch CARD_DETAILS_MODAL_CLOSED
		 */
		[MODAL_OPENED_SET]: ({ action, dispatch }) => {
			dispatch(CARD_DETAILS_MODAL_CLOSED, action.payload);
			action.stopPropagation();
		}
	},
	styles
});
