import { createCustomElement } from '@servicenow/ui-core';
import styles from './sn-vtb-card-dest-placeholder.scss';

/**
 * @seismicElement sn-vtb-card-dest-placeholder
 * @summary Component to be shown as a placeholder for card at the drop zone during the drag event.
 */
createCustomElement('sn-vtb-card-dest-placeholder', {
	view: ({ properties }) => (
		<div
			style={{
				height: `${properties.height}px`,
				width: `${properties.width}px`
			}}
		>
			<div className="sn-vtb-card-dest-placeholder" />
		</div>
	),
	properties: {
		width: { default: 0 },
		height: { default: 0 }
	},
	styles
});
