import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import './sn-vtb-record-form-header';
import '@servicenow/now-modal';
import '@servicenow/now-button';
import If from '../../shared/if';
import { t } from 'sn-translate';
import {
	MODAL_OPENED_SET,
	RECORD_STATUS_CHANGED,
	DATA_DRIVEN_CARD_FORM_MODAL_CLOSED,
	VTB_DATA_DRIVEN_CARD_ADDED,
	SEISMIC_COMPONENT_RENDERED
} from '../../../actions/constants';
import { querySelectorDeep } from '@devsnc/library-vtb-commons';

const view = (state) => {
	const { query, table, recordId, isVisible, view, errorMessage } = state.properties;
	const headerLabel = errorMessage ? t('Incident') : t('New Record');
	console.log('errorMessage', errorMessage);
	if (!isVisible) return null;

	return (
		/**
		 * Todo: Modal header label should be 'New ${table name} record'. We don't have table display value.
		 * Todo: Put the submit record action in the modal footer actions
		 */
		<now-modal size="lg" opened="true" header-label={headerLabel}>
			<now-record-form-section-connected table={table} sys-id={recordId} view={view} query={query}>
				<sn-vtb-record-form-header slot="header" message={errorMessage} />
			</now-record-form-section-connected>
		</now-modal>
	);
};

/**
 * @seismicElement sn-vtb-record-form-modal
 * @summary Component is a form modal to add a new card in data driven board.
 *
 */
createCustomElement('sn-vtb-record-form-modal', {
	view,

	properties: {
		/**
		 * Record query string to prepopulate fields in the form
		 * @type {String}
		 */
		query: {
			default: ''
		},
		/**
		 * Error message to be shown if any error occur.
		 * @type {String}
		 */
		errorMessage: {
			default: ''
		},
		/**
		 * Table where new record will be inserted
		 * @type {String}
		 */
		table: {
			default: ''
		},
		/**
		 * SysId of the record. for new record it is -1
		 * @type {Number}
		 */
		recordId: {
			default: -1
		},

		/**
		 * SysId of the board where new card will be added
		 * @type {String}
		 */
		boardId: {
			default: ''
		},
		/**
		 * Vertical lane id where the card will be added
		 * @type {String}
		 */
		laneId: {
			default: ''
		},
		/**
		 * Horizontal lane id where the card will be added
		 * @type {String}
		 */
		swimlaneId: {
			default: ''
		},
		/**
		 * Position of the card in a lane
		 * @type {String}
		 */
		position: {
			default: 'bottom'
		},
		/**
		 * Record view
		 * @type {String}
		 */
		view: {
			default: 'workspace'
		},
		/**
		 * show/hide Add card form modal
		 * @type {String}
		 */
		isVisible: {
			default: false
		}
	},
	actionHandlers: {
		[SEISMIC_COMPONENT_RENDERED]: ({ host }) => {
			setTimeout(() => {
				const dropdownEl = querySelectorDeep('.now-button-iconic', host);
				if (dropdownEl) dropdownEl.focus();
			}, 100);
		},
		/**
		 * On modal close, re-dispatches DATA_DRIVEN_CARD_FORM_MODAL_CLOSED event
		 * @param action handles MODAL_OPENED_SET
		 * @param dispatch DATA_DRIVEN_CARD_FORM_MODAL_CLOSED
		 */
		[MODAL_OPENED_SET]: ({ action, dispatch }) => {
			dispatch(DATA_DRIVEN_CARD_FORM_MODAL_CLOSED, action.payload);
		},
		/**
		 * Handles RECORD_STATUS_CHANGED event, when a new record is inserted in the table
		 *
		 * @param action RECORD_STATUS_CHANGED
		 * @param dispatch VTB_DATA_DRIVEN_CARD_ADDED when a new record is added in the table
		 * @param properties
		 */
		[RECORD_STATUS_CHANGED]: ({ action, dispatch, properties }) => {
			const { status, sys_id } = action.payload;
			if (status === 'inserted') {
				dispatch(VTB_DATA_DRIVEN_CARD_ADDED, {
					boardId: properties.boardId,
					taskId: sys_id,
					laneId: properties.laneId,
					swimlaneId: properties.swimlaneId,
					position: properties.position
				});
			}
		}
	}
});
