import { ARCHIVE_CARD, MOVE_CARD, APPLY_LABEL, ASSIGN_TO, CARD_ACTIONS } from '../../../constants/translated';
import { FREEFORM } from '../../../actions/constants';

export function getCardActionItems(cardId, boardType) {
	const getCardActionItem = [{ id: 'move_card', label: MOVE_CARD, uuid: cardId }];
	const getArchiveCardOption = [{ id: 'archive_card', label: ARCHIVE_CARD, uuid: cardId }];

	const dropdownItems = [{ id: 'cardActions_section', children: getCardActionItem, uuid: cardId }];
	if (boardType === FREEFORM)
		dropdownItems.unshift({ id: 'archiveCard_section', children: getArchiveCardOption, uuid: cardId });

	return dropdownItems;
}
