import { closestDeep, querySelectorDeep } from '@devsnc/library-vtb-commons';
import { ADD_CARD_CLICKED, FOCUS_LOST_FROM_BOARD } from '../../actions/constants';

const KEYS = {
	ESCAPE: 'Escape',
	F2: 'F2',
	TAB: 'Tab',
	ENTER: 'Enter'
};

// VTB specific elements for navigation
const VTB_CARD_SELECTOR = '.sn-vtb-card';
const NOW_CARD_SELECTOR = '.now-card';
const ADD_CARD_SELECTOR = '.add-card';
const ADD_LANE_SELECTOR = '.lane-add-btn';
const LANE_HEADER_SELECTOR = '.sn-vtb-lane-header';
const CARD_FIRST_ELEM_SELECTOR = 'button.now-dropdown';
const LANE_HEADER_FIRST_ELEM_SELECTOR = '.value-label ';
const SWIMLANE_HEADER_SELECTOR = '.sn-vtb-swimlane-header .container';
const SWIMLANE_HEADER_FIRST_ELEM_SELECTOR = '.value-label';

// Requirements:
// On pressing F2, enter the card or leave the card
// On pressing Escape, go back to the card wrapper
// On pressing Tab, skip the entire board and go to the tools group
// On pressing Shift Tab, skip the entire board and go back to Quick Filter pills

const focusInside = (el, selector, event) => {
	const internalElem = querySelectorDeep(selector, el);
	if (internalElem) {
		event.preventDefault();
		internalElem.focus();
	}
};

const focusOutside = (el, selector, event) => {
	const parentElement = closestDeep(el, selector);
	if (parentElement) {
		event.preventDefault();
		parentElement.focus();
	}
};

export const accessibilityKeyHandlers = {
	['NOW_VISUAL_BOARD#WRAPPER_KEY_DOWN']: ({ dispatch, action }) => {
		const { event, name } = action.payload;
		const path = event.composedPath ? event.composedPath() : event.path;
		const activeElement = path[0];

		if (name === 'sn-lane-header') {
			switch (event.key) {
				case KEYS.TAB:
					if (activeElement && (activeElement.matches(LANE_HEADER_SELECTOR) || activeElement.matches(ADD_LANE_SELECTOR))) {
						dispatch(FOCUS_LOST_FROM_BOARD, { element: activeElement, key: KEYS.TAB });
						event.preventDefault();
						activeElement.blur();
					}
					break;
				case KEYS.ESCAPE:
					// Bring focus back to the lane header elem
					if (activeElement && !activeElement.matches(LANE_HEADER_SELECTOR))
						focusOutside(activeElement, LANE_HEADER_SELECTOR, event);

					break;
				case KEYS.F2:
					// If lane header is currently focussed, enter the lane header (i.e focus inside)
					// Else focus back on the lane header
					if (activeElement && activeElement.matches(LANE_HEADER_SELECTOR)) {
						// Focus inside the card
						focusInside(activeElement, LANE_HEADER_FIRST_ELEM_SELECTOR, event);
					} else {
						// Focus back on the card itself
						focusOutside(activeElement, LANE_HEADER_SELECTOR, event);
					}
					break;
				default:
					break;
			}
		} else if (name === 'lane-footer-container') {
			switch (event.key) {
				case KEYS.TAB:
					if (activeElement && activeElement.matches(ADD_CARD_SELECTOR)) {
						dispatch(FOCUS_LOST_FROM_BOARD, { element: activeElement, key: KEYS.TAB });
						event.preventDefault();
						activeElement.blur();
					}
					break;
				case KEYS.ENTER:
					//Click Add card button
					if (activeElement && activeElement.matches(ADD_CARD_SELECTOR)) {
						const snLane = closestDeep(activeElement, 'sn-lane');
						const newCard = {
							position: 'bottom'
						};
						//Get laneId and swimlaneId of add-card button from dom
						//if swimlanes are enabled
						if (snLane.getAttribute('id').split('_')[0] !== 'lane') {
							newCard.swim_lane_id = snLane.getAttribute('id').split('_')[0];
							newCard.lane_id = snLane.getAttribute('id').split('_')[1];
						} else {
							newCard.lane_id = snLane.getAttribute('id').split('_')[1];
						}
						dispatch(ADD_CARD_CLICKED, { card: newCard });
					}
					break;
				case KEYS.ESCAPE:
					// Bring focus back to the lane header elem
					if (activeElement && !activeElement.matches(ADD_CARD_SELECTOR))
						focusOutside(activeElement, ADD_CARD_SELECTOR, event);
					break;
			}
		} else if (name === 'sn-card-wrapper') {
			switch (event.key) {
				case KEYS.TAB:
					if (activeElement && activeElement.matches(NOW_CARD_SELECTOR)) {
						dispatch(FOCUS_LOST_FROM_BOARD, { element: activeElement, key: KEYS.TAB });
						event.preventDefault();
						activeElement.blur();
					}
					break;
				case KEYS.ESCAPE:
					// Bring focus back to the card element
					if (activeElement && !activeElement.matches(NOW_CARD_SELECTOR)) {
						const snVtbCardElement = closestDeep(activeElement, VTB_CARD_SELECTOR);
						focusInside(snVtbCardElement, NOW_CARD_SELECTOR, event);
					}
					break;
				case KEYS.F2:
					// If card is currently focussed, enter the card (i.e focus inside)
					// Else focus back on the card
					if (activeElement && activeElement.matches(NOW_CARD_SELECTOR)) {
						// Focus inside the card
						const snVtbCardElement = closestDeep(activeElement, VTB_CARD_SELECTOR);
						focusInside(snVtbCardElement, CARD_FIRST_ELEM_SELECTOR, event);
					} else {
						// Focus back on the card itself
						const snVtbCardElement = closestDeep(activeElement, VTB_CARD_SELECTOR);
						focusInside(snVtbCardElement, NOW_CARD_SELECTOR, event);
					}
					break;
			}
		} else if (name === 'sn-swimlane-header') {
			switch (event.key) {
				case KEYS.TAB:
					if (activeElement && activeElement.matches(SWIMLANE_HEADER_SELECTOR)) {
						dispatch(FOCUS_LOST_FROM_BOARD, { element: activeElement, key: KEYS.TAB });
						event.preventDefault();
						activeElement.blur();
					}
					break;
				case KEYS.ESCAPE:
					// Bring focus back to the lane header elem
					if (activeElement && !activeElement.matches(SWIMLANE_HEADER_SELECTOR))
						focusOutside(activeElement, SWIMLANE_HEADER_SELECTOR, event);

					break;
				case KEYS.F2:
					// If lane header is currently focussed, enter the lane header (i.e focus inside)
					// Else focus back on the lane header
					if (activeElement && activeElement.matches(SWIMLANE_HEADER_SELECTOR)) {
						// Focus inside the card
						focusInside(activeElement, SWIMLANE_HEADER_FIRST_ELEM_SELECTOR, event);
					} else {
						// Focus back on the card itself
						focusOutside(activeElement, SWIMLANE_HEADER_SELECTOR, event);
					}
					break;
			}
		}
	}
};
