import { createCustomElement } from '@servicenow/ui-core';
import styles from './assignee-grouped.scss';
import '@servicenow/now-avatar';

const view = (state) => {
	const { assignees, visibleCount: avatarCount, size } = state.properties;
	let visibleCount = parseInt(avatarCount);
	let users = [];
	if (assignees.length <= visibleCount + 1) {
		assignees.forEach((user) => users.push(getAvatar(user)));
	} else {
		for (let i = 0; i < visibleCount; i++) {
			users.push(getAvatar({ avatar: assignees[i].avatar, name: assignees[i].name }));
		}
		users.push(getAvatarNumber(assignees.length - visibleCount));
	}

	function getAvatarNumber(totalUsers) {
		if (!totalUsers) return;
		return <span className="assignee-avatar-total">+{totalUsers}</span>;
	}

	function getAvatar(user) {
		if (!user) return;
		return (
			<now-avatar
				className={'assignee-avatar -' + size}
				size={size}
				image-src={'' + user.avatar}
				user-name={user.name}
			/>
		);
	}

	return <div className="vtb-assignee-list">{users}</div>;
};

createCustomElement('sn-vtb-assignee-grouped', {
	view,
	properties: {
		assignees: { default: [] },
		visibleCount: { default: 1 },
		size: { default: 'sm' }
	},
	styles
});
