import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-record-field-lookup-connected';
import '../../shared/sn-inline-edit/sn-inline-edit';
import If from '../../../shared/if';
import styles from './sn-vtb-add-swimlane.scss';
import {
	BUTTON_CLICKED,
	LANE_CREATE_REQUEST,
	NOW_RECORD_FIELD_VALUE_CHANGED,
	GUIDED,
	VTB_ADD_SWIMLANE_VALUE_UPDATE_CANCELLED,
	VTB_ADD_SWIMLANE_VALUE_UPDATED,
	VTB_ADD_SWIMLANE_REFERENCE_PICKER_BLUR
} from '../../../../actions/constants';
import { ADD_SWIMLANE } from '../../../../constants/translated';
import { t } from 'sn-translate';
import '../../shared/sn-vtb-reference-picker/sn-vtb-reference-picker';
import { querySelectorDeep } from '@devsnc/library-vtb-commons';
import '@servicenow/now-tooltip';

const view = (state, { dispatch, updateState }) => {
	const {
		addSwimlaneEditing,
		properties: { data: swimlane }
	} = state;
	const setTargetRef = (ref) => updateState({ targetRef: ref });
	const { options, swimlanes_reference_table_records, addLaneButton } = swimlane;
	const { reference_swim_lane_search_field, swim_lane_field_reference_table } = options;
	const addNewReferenceSwimlaneQuery =
		reference_swim_lane_search_field +
		'STARTSWITH^' +
		reference_swim_lane_search_field +
		'ISNOTEMPTY^sys_idNOT IN' +
		swimlanes_reference_table_records;
	return (
		<div className="swimlane-add collapsed">
			<hr className={'separator ' + (addLaneButton ? 'till-add-lane' : '')} />
			<div className="container">
				<button
					className={addSwimlaneEditing ? 'swimlane-add-btn-hide' : 'swimlane-add-btn'}
					hook-insert={({ elm }) => setTargetRef(elm)}
					hook-destroy={() => setTargetRef(null)}
					on-click={() => dispatch(BUTTON_CLICKED)}
					aria-label={ADD_SWIMLANE}
				>
					<div className="swimlane-add-btn-content">
						<now-icon className="swimlane-add-btn-icon" icon="plus-outline" aria-hidden="true" />
						<span className="swimlane-add-btn-label">{ADD_SWIMLANE}</span>
					</div>
				</button>
				<now-tooltip target-ref={state.targetRef} content={ADD_SWIMLANE} />

				<If test={addSwimlaneEditing && options.board_type !== GUIDED}>
					<sn-inline-edit
						className="swimlane-add-sn-inline-edit"
						name="VTB_ADD_SWIMLANE"
						value={''}
						editing={true}
						config-aria={{
							label: { ariaLabel: t('Add new swimlane') }
						}}
					/>
				</If>
				<If test={addSwimlaneEditing && options.board_type === GUIDED}>
					<sn-vtb-reference-picker
						name="VTB_ADD_SWIMLANE"
						className="swimlane-add-reference-picker"
						tableName={swim_lane_field_reference_table}
						fieldName={reference_swim_lane_search_field}
						aria-label={ADD_SWIMLANE}
						placeholder={t('Add New Swimlane')}
						autofocus={true}
						additionalQueryString={addNewReferenceSwimlaneQuery}
					/>
				</If>
			</div>
		</div>
	);
};

/**
 * @seismicElement sn-vtb-add-swimlane
 * @summary Component to be rendered in the add swimlane slot.
 */
export default createCustomElement('sn-vtb-add-swimlane', {
	view,
	properties: {
		/**
		 * Swimlane data object.
		 * @type {Object}
		 */
		data: {
			default: {}
		}
	},
	actionHandlers: {
		[BUTTON_CLICKED]: ({ updateState }) => {
			updateState({
				path: 'addSwimlaneEditing',
				value: true,
				operation: 'set'
			});
		},
		[VTB_ADD_SWIMLANE_VALUE_UPDATED]: ({ dispatch, action, updateState, properties }) => {
			const {
				data: { board_id, options }
			} = properties;
			let {
				payload: { newValue }
			} = action;
			newValue = newValue.trim();
			const payload = {
				createLaneInput: {
					boardId: board_id,
					value: '',
					name: newValue,
					order: options.highest_swimlane_order + 1,
					isSwimLane: true
				}
			};
			updateState({
				path: 'addSwimlaneEditing',
				value: false,
				operation: 'set'
			});
			if (newValue.length !== 0) dispatch(LANE_CREATE_REQUEST, payload.createLaneInput);
		},
		[VTB_ADD_SWIMLANE_VALUE_UPDATE_CANCELLED]: ({ host, updateState }) => {
			updateState({
				path: 'addSwimlaneEditing',
				value: false,
				operation: 'set'
			});
			setTimeout(() => {
				querySelectorDeep('button.swimlane-add-btn', host).focus();
			}, 100);
		},
		[NOW_RECORD_FIELD_VALUE_CHANGED]: ({ action, dispatch, properties, updateState }) => {
			const {
				data: { board_id, options }
			} = properties;
			if (action.payload.value && action.payload.value !== '') {
				const payload = {
					createLaneInput: {
						boardId: board_id,
						value: action.payload.value,
						name: action.payload.displayValue,
						order: options.highest_swimlane_order + 1,
						isSwimLane: true
					}
				};
				dispatch(LANE_CREATE_REQUEST, payload.createLaneInput);
				updateState({
					path: 'addSwimlaneEditing',
					value: false,
					operation: 'set'
				});
			}
		},
		//On input blur when adding reference swimlane
		[VTB_ADD_SWIMLANE_REFERENCE_PICKER_BLUR]: ({ host, updateState }) => {
			updateState({
				path: 'addSwimlaneEditing',
				value: false,
				operation: 'set'
			});
			setTimeout(() => {
				querySelectorDeep('button.swimlane-add-btn', host).focus();
			}, 100);
		},
		[actionTypes.COMPONENT_DOM_READY]: ({ host, dispatch }) => {
			const buttonEl = querySelectorDeep('.swimlane-add-btn', host);
			if (buttonEl) dispatch('REGISTER_ARROW_NAVIGABLE_ELEMENT', buttonEl);
		}
	},
	styles,
	initialState: {
		addSwimlaneEditing: false
	}
});
