export function getTruncatedSla(sla) {
	if (!(sla && sla.percentage)) return;
	let str = sla.percentage;
	str = str + '%';
	let length = 6;
	let ending = '..%';
	if (str.length > length) {
		return str.substring(0, length - ending.length) + ending;
	} else {
		return str;
	}
}

export function getSlaColor(sla) {
	if (!(sla && sla.percentage)) return;
	const percentage = sla.percentage;
	if (percentage <= 50) return 'positive';
	else if (percentage > 50 && percentage <= 90) return 'warning';
	else if (percentage > 90 && percentage < 100) return 'high';

	return 'critical';
}
