import {
	ATTACHMENT_UPLOADED,
	NOW_VISUAL_BOARD_CARD_MOVED,
	VTB_CARD_MOVED,
	VTB_ATTACHMENT_UPLOADED
} from '../../constants';
import addCardHandlers from './addCardHandlers';
import archiveCardActionHandlers from './archiveCardHandlers';

export default {
	[NOW_VISUAL_BOARD_CARD_MOVED]: ({ action, dispatch }) => {
		const { payload } = action;
		dispatch(VTB_CARD_MOVED, payload);
		action.stopPropagation();
	},
	[ATTACHMENT_UPLOADED]: ({ action, dispatch }) => {
		const { payload } = action;
		dispatch(VTB_ATTACHMENT_UPLOADED, payload);
		action.stopPropagation();
	},
	...addCardHandlers,
	...archiveCardActionHandlers
};
