import interact from 'interactjs';
import { actionTypes } from '@servicenow/ui-core';
import { querySelectorDeep } from '@devsnc/library-vtb-commons';
import {
	ATTACHMENT_HOVERED,
	ATTACHMENT_UNHOVERED,
	ATTACHMENT_DROPPED,
	ATTACHMENT_UPLOADED
} from '../../../actions/constants';

export default {
	name: 'ijs-droppable',
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: (args) => {
			const { host, dispatch, action } = args;
			const options = action.payload.options;
			const DROP_ITEM = options.prefix;
			setTimeout(() => {
				const el = querySelectorDeep('.sn-vtb-card', host);
				if (el) {
					interact(el).dropzone({
						accept: options.allowFrom,
						//Hack to prevent drop action on ESC press when target is hovering over card.
						checker: (dragEvent, event, dropped) => dropped && !event.target.classList.contains('escape'),
						ondragenter: () => dispatch(DROP_ITEM + '_HOVERED'),
						ondragleave: () => dispatch(DROP_ITEM + '_UNHOVERED'),
						ondrop: (event) => {
							dispatch(DROP_ITEM + '_UNHOVERED');
							dispatch(DROP_ITEM + '_DROPPED', event.relatedTarget.label);
						}
					});
				}
			}, 200);
		}
	}
};

export const attachmentDrop = {
	name: 'attachment-droppable',
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: (args) => {
			const { host, dispatch, action } = args;
			const el = querySelectorDeep('.sn-vtb-card', host);
			let vtbCardElement; // This is done to ignore the event bubbling up from the children of `.sn-vtb-card`
			if (el) {
				el.addEventListener('dragover', (event) => {
					if (event.dataTransfer.types.includes('Files')) {
						event.preventDefault();
						vtbCardElement = event.target; // Guaranteed to be `.sn-vtb-card` since that will be the last element to override this variable
						dispatch(ATTACHMENT_HOVERED);
					}
				});

				el.addEventListener('dragleave', (event) => {
					if (event.dataTransfer.types.includes('Files') && event.target === vtbCardElement) {
						dispatch(ATTACHMENT_UNHOVERED);
					}
				});

				el.addEventListener('dragend', (event) => {
					if (event.dataTransfer.types.includes('Files') && event.target === vtbCardElement) {
						dispatch(ATTACHMENT_UNHOVERED);
					}
				});

				el.addEventListener('drop', (event) => {
					if (event.dataTransfer.types.includes('Files') && event.target === vtbCardElement) {
						event.preventDefault();
						dispatch(ATTACHMENT_UNHOVERED);
						dispatch(ATTACHMENT_DROPPED, { files: event.dataTransfer.files });
						vtbCardElement = '';
					}
				});
			}
		},
		[ATTACHMENT_HOVERED]: ({ updateState }) =>
			updateState({
				attachmentHovering: true
			}),
		[ATTACHMENT_UNHOVERED]: ({ updateState }) =>
			updateState({
				attachmentHovering: false
			}),
		[ATTACHMENT_DROPPED]: ({ properties, dispatch, action, updateState }) => {
			const files = action.payload.files;
			const {
				data: {
					record: { sys_class_name, sys_id }
				}
			} = properties;
			for (let i = 0; i < files.length; i++) {
				let form = new FormData();
				form.append('sysparm_table', sys_class_name);
				form.append('sysparm_sys_id', sys_id);
				form.append('sysparm_encryption_context', '');
				form.append('sysparm_nostack', 'yes');
				form.append('attachments_modified', 'true');
				form.append('file', files[i]);
				let payload = {
					sysparm_type: 'ngk_attachments',
					action: 'add',
					load_attachment_record: 'true',
					table: sys_class_name,
					sys_id,
					data: form
				};
				updateState({
					currentFileName: files[i].name
				});
				dispatch(ATTACHMENT_UPLOADED, payload);
			}
		}
	}
};
