import {
	CONFIRM_LANE_DELETE,
	NOW_VISUAL_BOARD_LANE_MOVED,
	LANE_CREATE_REQUEST,
	LANE_HEADER_VALUE_UPDATED,
	LANE_HIDE_SELECTED,
	VTB_LANE_HIDE_SELECTED,
	VTB_LANE_MOVED,
	VTB_CREATE_LANE_REQUEST,
	VTB_LANE_HEADER_UPDATED,
	VTB_LANE_DELETED,
} from '../../constants';

export default {
	[LANE_CREATE_REQUEST]: ({ action, dispatch }) => {
		const { payload } = action;
		dispatch(VTB_CREATE_LANE_REQUEST, payload);
		action.stopPropagation();
	},
	[LANE_HEADER_VALUE_UPDATED]: ({ action, dispatch }) => {
		let {
			payload: { newValue, id }
		} = action;
		newValue = newValue.trim();
		const payload = {
			updateLaneInput: {
				name: newValue,
				laneId: id
			}
		};
		dispatch(VTB_LANE_HEADER_UPDATED, payload.updateLaneInput);
		action.stopPropagation();
	},
	[LANE_HIDE_SELECTED]: ({ action, dispatch, properties }) => {
		dispatch(VTB_LANE_HIDE_SELECTED, action.payload);
		action.stopPropagation();
	},
	[NOW_VISUAL_BOARD_LANE_MOVED]: ({ action, dispatch }) => {
		const { payload } = action;
		dispatch(VTB_LANE_MOVED, payload);
		action.stopPropagation();
	},
	[CONFIRM_LANE_DELETE]: ({ dispatch, action }) => {
		dispatch(VTB_LANE_DELETED, action.payload);
		action.stopPropagation();
	}
};
