import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { SnTimeAgo } from 'sn-component-timeago';
import styles from './sn-vtb-card-footer.scss';
import { TEXT_LINK_CLICKED } from '../../../../actions/constants';
import { UNASSIGNED, UPDATED } from '../../../../constants/translated';
import '@servicenow/now-avatar';
import '@servicenow/now-text-link';
import '@servicenow/now-label-value';
import '@servicenow/now-tooltip';
import '../../shared/assignees-list-grouped/assignee-grouped';
import If from '../../../shared/if';
import isEqual from 'lodash/isEqual';

const { COMPONENT_DOM_READY, COMPONENT_DISCONNECTED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const updateTimeAgoInstance = (oldTimeAgoInstance, timeStamp, updateState) => {
	if (!timeStamp) return;

	if (oldTimeAgoInstance) oldTimeAgoInstance.remove();

	// Use sys_updated_on.value along with systemTime option to make it work without using SnTimeAgo behaviors
	const timeAgoInstance = new SnTimeAgo({}, timeStamp, {
		timeAgo: true,
		systemTime: true
	});
	const displayTime = timeAgoInstance.getTickTime().words;
	updateState({
		timeAgoWords: displayTime,
		timeAgoInstance: timeAgoInstance
	});

	// Callback function to keep updating the ticktime
	timeAgoInstance.setUpdateFn((tickTime) => {
		updateState({
			timeAgoWords: tickTime.words
		});
	});
};

const view = (state, { updateState }) => {
	const {
		task_id,
		users,
		record: {
			number: task,
			sys_updated_on: { display_value: updatedOnDisplayValue }
		}
	} = state.properties.data;
	const { timeAgoWords, timeAgoContainer } = state;
	const assignees = users.primary_assignees.concat(users.additional_assignees);
	const setTargetRef = (ele) => setTimeout(() => updateState({ timeAgoContainer: ele }), 100);

	return (
		<div className="sn-vtb-card-footer">
			<div className="vtb-avatar">
				<If test={assignees.length === 0}>
					<div className="avatar-unassigned">
						<now-avatar size="sm" user-name=" " ariaHidden="false" />
						<span className="unassigned-text">{UNASSIGNED}</span>
					</div>
				</If>
				<If test={assignees.length > 0}>
					<sn-vtb-assignee-grouped assignees={assignees} visible-count="1" />
				</If>
			</div>

			<div className="footer-items-right">
				<div className="record-number">
					<now-text-link
						label={task.value}
						append-to-payload={{ task_id }}
						variant="secondary"
						href="javascript:void(0)"
					/>
				</div>
				<div
					className="time-ago-container"
					hook-insert={({ elm }) => setTargetRef(elm)}
					hook-destroy={() => setTargetRef(null)}
				>
					<If test={timeAgoWords}>
						<now-label-value-inline truncated label={UPDATED} value={timeAgoWords} />
						<now-tooltip target-ref={timeAgoContainer}>
							<time>{updatedOnDisplayValue}</time>
						</now-tooltip>
					</If>
				</div>
			</div>
		</div>
	);
};

/**
 * @seismicElement sn-vtb-card-footer
 * @summary Component to be rendered in the card footer slot.
 */
createCustomElement('sn-vtb-card-footer', {
	view,
	properties: {
		/**
		 * Card data object
		 * @type {Object}
		 */
		data: { default: {} }
	},
	actionHandlers: {
		[TEXT_LINK_CLICKED]: ({ action, dispatch }) => dispatch('VTB#OPEN_RECORD', action.payload.task_id),
		[COMPONENT_DOM_READY]: ({ properties, updateState, state }) => {
			const {
				sys_updated_on: { value: timeStamp }
			} = properties.data.record;
			const { timeAgoInstance } = state;
			updateTimeAgoInstance(timeAgoInstance, timeStamp, updateState);
		},
		[COMPONENT_DISCONNECTED]: ({ state }) => {
			const { timeAgoInstance } = state;
			timeAgoInstance.remove();
		},
		[COMPONENT_PROPERTY_CHANGED]: ({ action, updateState, state }) => {
			const {
				value: {
					record: {
						sys_updated_on: newValue,
						sys_updated_on: { value: timeStamp }
					}
				},
				previousValue: {
					record: { sys_updated_on: oldValue }
				}
			} = action.payload;

			if (!isEqual(oldValue, newValue)) {
				const { timeAgoInstance } = state;
				updateTimeAgoInstance(timeAgoInstance, timeStamp, updateState);
			}
		}
	},
	styles
});
